import React, {Component} from 'react';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';

import * as AdminAction from '../../redux/actions/adminAction';

import { Button }  from '@material-ui/core';

class AdminContentTop extends Component{
    onToggleAdminSideBar = () => {
        this.props.toggleAdminSideBar()
    }
    render(){
        return(
            <div className="AdminContentTop">
                <div class={`toggleMenu ${this.props.admin.showSideBar}`} onClick={this.onToggleAdminSideBar}>
                    <svg stroke="currentColor" fill="currentColor" stroke-width="0" viewBox="0 0 448 512" height="1em" width="1em" xmlns="http://www.w3.org/2000/svg">
                        <path d="M16 132h416c8.837 0 16-7.163 16-16V76c0-8.837-7.163-16-16-16H16C7.163 60 0 67.163 0 76v40c0 8.837 7.163 16 16 16zm0 160h416c8.837 0 16-7.163 16-16v-40c0-8.837-7.163-16-16-16H16c-8.837 0-16 7.163-16 16v40c0 8.837 7.163 16 16 16zm0 160h416c8.837 0 16-7.163 16-16v-40c0-8.837-7.163-16-16-16H16c-8.837 0-16 7.163-16 16v40c0 8.837 7.163 16 16 16z">
                        </path>
                    </svg>
                </div>
                <div className="AdminContentTop-Title">
                    <h3>{this.props.admin.title}</h3>
                </div>
            </div>
        )
    }
}
const mapStateToProps = (state) =>({
    admin:state.admin
})

function mapDispatchToProps(dispatch)
{
    return bindActionCreators({
        toggleAdminSideBar:AdminAction.toggleAdminSideBar,
    }, dispatch);
}
export default connect(mapStateToProps,mapDispatchToProps)(AdminContentTop)