import React, {Component} from 'react';

import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import {Link} from 'react-router-dom';

import * as AuthAction from '../../redux/actions/authAction';

import {Col, Container, Row} from 'reactstrap'

import "./MyInquiry.css";
import MyCheckBox from '../../components/Utility/MyCheckBox'

const stateString = [
    'PaymentProof',
    'ShipmentSchedule',
    'Done',
    '',
    '',
    '',
    'Expired'
]
class MyOrderItem extends Component{

    render(){
        const { orders } = this.props
        return(
           <Row className="TabContent">
                {(
                    <div className="TablePart">
                    <div className="Table-Item TableHeader">
                        <div className="TableContent-Row-Header">
                            <div className="Table-Item TableHeader-Item THI-Index">
                                <MyCheckBox></MyCheckBox>
                            </div>
                            <div className="Table-Item TableHeader-Item THI-Normal">
                                PICTURE
                            </div>
                            <div className="Table-Item TableHeader-Item THI-Normal">
                                CAR DETAILS
                            </div>
                            <div className="Table-Item TableHeader-Item THI-Normal">
                                TOTAL PRICE
                            </div>
                            <div className="Table-Item TableHeader-Item THI-Normal">
                                UPDATED
                            </div>
                            <div className="Table-Item TableHeader-Item THI-Normal">
                                STATUS
                            </div>
                        </div>
                    </div>
                    <div className="TableContent">
                    {Array.isArray(orders) && (
                            <>
                            {orders.length != 0 && (
                                <>
                                    {orders.map((item,index)=>{
                                
                                    const imgpath = item.inquiry.car.images.length == 0 ? "":item.inquiry.car.images[0]
                                    let i = -1
                                    
                                    let hasNewMsg = false
                                    item.chatlogs.map((chat,index)=>{
                                        console.log(chat)
                                        if( chat.receiver._id == this.props.auth.user.id && chat.state == 1){
                                                hasNewMsg = true
                                        }
                                    })
                                    return(
                                        <div className="TableContent-Row">
                                            <div className="Table-Item TableContent-Item THI-Index">
                                                <MyCheckBox></MyCheckBox>
                                            </div>
                                            <div className="Table-Item TableContent-Item THI-Normal">
                                                <div className="Table-Item-P">
                                                    <img src={imgpath}></img>
                                                </div>
                                                <div className="Table-Item-P">
                                                    <p>INQUIRY</p>
                                                </div>
                                                <div className="Table-Item-P">
                                                    <Link to={{
                                                            pathname:`/detail/${item.inquiry.car._id}`,
                                                            
                                                    }}>View Details</Link>
                                                </div>
                                            </div>
                                            <div className="Table-Item TableContent-Item THI-Normal">
                                                <div className="Table-Item-P">
                                                    <p>{item.inquiry.car.make} {item.inquiry.car.model}</p>
                                                </div>
                                                <div className="Table-Item-P">
                                                    <p className="Opacity70">Ref No. CFG3804567</p>
                                                </div>
                                                <div className="Table-Item-P">
                                                    <p className="Opacity70">{item.inquiry.car.odometer} m.</p>
                                                </div>
                                                <div className="Table-Item-P">
                                                    <p className="Opacity70">{item.inquiry.car.engine_size} {item.inquiry.car.engine_size_unit}</p>
                                                </div>
                                            </div>
                                            <div className="Table-Item TableContent-Item THI-Normal">
                                                <div className="Table-Item-P">
                                                    <p className="">Australia / Kembla (Port)</p>
                                                </div>
                                                <div className="Table-Item-P">
                                                    <p className="Opacity70">with Inspection</p>
                                                </div>
                                            </div>
                                            <div className="Table-Item TableContent-Item THI-Normal">
                                                <div className="Table-Item-P">
                                                    <p className="">{item.inquiry.car.updated}</p>
                                                </div>
                                                <div className="Table-Item-P">
                                                    <p className="">11:00 AM</p>
                                                </div>
                                            </div>
                                            <div className="Table-Item TableContent-Item THI-Normal">
                                                <div className="Table-Item-P">
                                                    {stateString[item.inquiry.state - 4]}                                                                                   
                                                    <a style={{cursor:'pointer'}} onClick={()=>this.props.setToggle(item.inquiry._id,true)} className="">
                                                        <>
                                                        <div className={hasNewMsg?`NEWMSG-ALARM`:`NONEWMSG-ALARM`}><div>Chat</div></div>
                                                        </>
                                                    </a>
                                                
                                                </div>
                                            </div>
                                        </div>
                                    )
                                })}
                                </>
                            )}
                             {orders.length == 0 && (
                                 <div className="Empty-Content">
                                     No Data
                                </div>
                            )}
                        
                        </>
                    )}
                     {!Array.isArray(orders) && (
                            <div className="Empty-Content">
                                No Data
                            </div>
                        )}
                    </div>
                </div>
                )}
            </Row>
        )
    }
}
const mapStateToProps = (state) =>({
    auth            :state.auth,
})

function mapDispatchToProps(dispatch)
{
    return bindActionCreators({      

      // showDlg:GlobalAction.showDialog
      //  registerUser :AuthAction.registerUser
    }, dispatch);
}
export default connect(mapStateToProps,mapDispatchToProps)(MyOrderItem)
