import React, {Component} from 'react';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import {Link} from 'react-router-dom';

import * as AuthAction from '../../redux/actions/authAction';
import * as AdminAction from '../../redux/actions/adminAction';

import { ProSidebar, Menu, MenuItem, SubMenu,SidebarHeader, SidebarFooter, SidebarContent  } from 'react-pro-sidebar';
import SocketContext from '../../socket-context'

import {Col, Container, Row} from 'reactstrap'

import 'react-pro-sidebar/dist/css/styles.css'
import './style.css'
class MyIcon extends Component{
    render(){
        return(
            <div>1</div>
        )
    }
}
class AdminSideBar extends Component{
    componentDidMount(){
          /** */
          this.props.socket.emit("pushuser",{
            user:this.props.auth.user
        })
        /** */

        this.props.socket.on("__sendInvoiceMsg",this.onSendInvoiceMsg)
        this.props.socket.on("__successSendInvoiceMsgFromAdmin",this.onSuccessSendInvoiceMsgFromAdmin)
        
        this.props.socket.on("__refreshFlowChartFromUser",this.onRefreshFlowChartFromUser)

        this.props.getInquiryList()
    }
    componentWillUnmount(){
        this.props.socket.off("__sendInvoiceMsg",this.onSendInvoiceMsg)
        this.props.socket.off("__successSendInvoiceMsgFromAdmin",this.onSuccessSendInvoiceMsgFromAdmin)
        this.props.socket.off("__refreshFlowChartFromUser",this.onRefreshFlowChartFromUser)

    }
    //socket
    onSendInvoiceMsg = (data)=>{
        this.props.addInvoiceMsg(data.chatlog)
    }
    onSuccessSendInvoiceMsgFromAdmin = (data)=>{
        this.props.addInvoiceMsg(data.chatlog)
    }
    onRefreshFlowChartFromUser = (data)=>{
        this.props.refreshFlow({
            inquiryId:data.inquiryId
        })
    }
    //
    toggleSideBar = () =>{
        this.props.toggleAdminSideBar()
    }
    render(){
        const { showSideBar , totalInquiry } = this.props.admin
        let orderLen = 0
        let inquirLen = 0
        let inquiryNewMsg = 0
        let orderNewMsg = 0
        if(Array.isArray(totalInquiry)){
            totalInquiry.map((item,index)=>{
                if(item.inquiry.state > 2){
                    orderLen++
                    item.chatlogs.map((chat,index)=>{
                        if( chat.receiver._id == this.props.auth.user.id && chat.state == 1){
                            orderNewMsg++
                        }
                    })
                }else
                {
                    inquirLen++
                    item.chatlogs.map((chat,index)=>{
                        if( chat.receiver._id == this.props.auth.user.id && chat.state == 1){
                            inquiryNewMsg++
                        }
                    })
                }
            })
        }
        
        return(
            
                <ProSidebar collapsed={false} breakPoint={'md'} toggled={showSideBar} onToggle={this.toggleSideBar}>
                    <SidebarHeader>
                       <p className="logo">
                           LOGO
                        </p>
                    </SidebarHeader>
                    <SidebarContent>
                        <Menu iconShape="square">
                            <MenuItem>
                                <Link className="menu" to="/admin/">
                                    <div className="menuText">Dashboard</div>
                                </Link>
                            </MenuItem>
                            <MenuItem>
                                <Link className="menu" to="/admin/request">
                                    <div className="menuText">Request</div>
                                    <div className="badge">
                                        
                                            <div className="badge menuSub greenColor">
                                                {inquirLen}
                                            </div>
                                            {inquiryNewMsg != 0 && (
                                                <div className="badge menuSub redColor">
                                                {inquiryNewMsg}
                                                </div>
                                            )}
                                            
                                        
                                        {/* <div className="badge menuSub redColor">12</div> */}
                                    </div>
                                </Link>
                            </MenuItem>
                            <MenuItem>
                                <Link className="menu"  to="/admin/order">
                                    <div className="menuText">Order</div>
                                    <div className="badge">
                                        
                                            <div className="badge menuSub greenColor">
                                                {orderLen}
                                            </div>
                                            {orderNewMsg != 0 && (
                                                <div className="badge menuSub redColor">
                                                {orderNewMsg}
                                                </div>
                                            )}
                                        
                                        {/* <div className="badge menuSub redColor">12</div> */}
                                    </div>
                                </Link>
                            </MenuItem>
                            <MenuItem>
                                <Link className="menu"  to="/admin/car">
                                    <div className="menuText">CarManage</div>
                                </Link>
                            </MenuItem>
                            <MenuItem>
                                <Link className="menu"  to="/">
                                    <div className="menuText">Home</div>
                                </Link>
                            </MenuItem>
                            <MenuItem>
                                
                                    <div className="menuText" onClick={()=>{this.props.signout()}}>SignOut</div>
                                
                            </MenuItem>
                        </Menu>
                    </SidebarContent>
                    <SidebarFooter>
                        {/**
                         *  You can add a footer for the sidebar ex: copyright
                         */}
                    </SidebarFooter>
                </ProSidebar>
            
        )
    }
}
const AdminSideBarWithSocket = (props) => (
    <SocketContext.Consumer>
        {socket => <AdminSideBar {...props} socket={socket}/>}
    </SocketContext.Consumer>
)
const mapStateToProps = (state) =>({
    auth:state.auth,
    admin:state.admin,
})

function mapDispatchToProps(dispatch)
{
    return bindActionCreators({
        signout:AuthAction.logoutUser,
        toggleAdminSideBar:AdminAction.toggleAdminSideBar,
        getInquiryList:AdminAction.getInvoiceList,
        addInvoiceMsg:AdminAction.addInvoiceMsg,
        refreshFlow      :AdminAction.refreshFlow


    }, dispatch);
}
export default connect(mapStateToProps,mapDispatchToProps)(AdminSideBarWithSocket)

