import React, {Component} from 'react';

import { 
    Dialog,
    DialogActions,
    DialogContent,
    DialogContentText,
    DialogTitle,
    Button,
    Slide
} from '@material-ui/core';

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
  });

class AdminDocSettingDialog extends Component{
    render(){
        const { 
            open, 
            handleClose,
            doc,
            agree,
            disagree
        } = this.props
        return(
            <Dialog
                open={open}
                onClose={handleClose}
                TransitionComponent={Transition}
                aria-labelledby="responsive-dialog-title"
            >
                <DialogTitle id="responsive-dialog-title">{"Agree?"}</DialogTitle>
                <DialogContent>
                <DialogContentText>
                    Can you accept this doc?
                </DialogContentText>
                </DialogContent>
                <DialogActions>
                <Button autoFocus onClick={disagree} color="primary">
                    No
                </Button>
                <Button onClick={agree} color="primary" autoFocus>
                    Yes
                </Button>
                </DialogActions>
            </Dialog>
        )
    }
}
export default AdminDocSettingDialog