import React, {Component} from 'react'
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import * as AdminAction from '../../../redux/actions/adminAction';

import { Card , CardContent , CardHeader} from '@material-ui/core';
import {Col, Container, Row} from 'reactstrap'
import '../Admin.css'

class AdminDashboard extends Component{
    componentDidMount(){
        this.props.setTitle("Dashboard")
    }
    componentWillReceiveProps(nextProps) {
        if (!nextProps.auth.isAuthenticated) {
            this.props.history.push("/");
        }
    }
    render(){
        const { totalInquiry } = this.props.admin

        let orderLen = 0
        let inquirLen = 0
        if(Array.isArray(totalInquiry)){
            totalInquiry.map((item,index)=>{
                if(item.inquiry.state > 2){
                    orderLen++
                }else
                {
                    inquirLen++
                }
            })
        }
        return(
            <>
            <Row>
                <Col md={4} sm={12}>
                    <Card>
                        <div className="AdminDashboardCardTitle"> 
                            REQUEST
                        </div>
                        <CardContent>
                            <div style={{fontSize: '40px'}}>
                                {inquirLen}
                            </div>
                        </CardContent>
                    </Card>
                </Col>
                <Col md={4} sm={12}>
                    <Card>
                        <div className="AdminDashboardCardTitle"> 
                            ORDER
                        </div>
                        <CardContent>
                            <div style={{fontSize: '40px'}}>
                                {orderLen}
                            </div>
                        </CardContent>
                    </Card>
                </Col>
                <Col md={4} sm={12}>
                </Col>
            </Row>
            <Row>
            </Row>
            </>
        )
    }
}
const mapStateToProps = (state) =>({
    auth        :state.auth,
    admin:state.admin,

})

function mapDispatchToProps(dispatch)
{
    return bindActionCreators({
        setTitle:AdminAction.setTitle
        //getOrderInvoice:AdminAction.getOrderInvoice
        // getAskInvoiceCar:AdminAction.getAskInvoiceCar ,
        // addInvoiceMsg:AdminAction.addInvoiceMsg
    }, dispatch);
}
export default connect(mapStateToProps,mapDispatchToProps)(AdminDashboard)