import React, {Component} from 'react';

import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import * as InquiryAction from '../../redux/actions/inquiryAction';
import * as GlobalAction from '../../redux/actions/globalAction';
import * as AuthAction from '../../redux/actions/authAction';
import { animateScroll } from "react-scroll";
import {Col, Container, Row} from 'reactstrap'
import { TextField , Button }  from '@material-ui/core';
import SocketContext from '../../socket-context'

import "./MyInquiry.css";

class AskInquiry extends Component{
    constructor(props){
        super(props)
        const messagesEnd = React.createRef()

    }
    state={
        msg:'',
        chatlogs:[]
    }
    
    componentDidMount(){
       let item = this.getCar()
       this.props.socket.emit('checkDoneInquiryMsg',{
            inquiry:item.inquiry._id,
            receiver:this.props.auth.user.id
        })
       this.props.socket.on('__sendInvoiceMsgFromAdmin',data=>{
            this.props.socket.emit('checkDoneInquiryMsg',{
                inquiry:item.inquiry._id,
                receiver:this.props.auth.user.id

            })
        })
        this.props.socket.on('__checkDoneInquiryMsg',data=>{
            this.props.checkDoneInquiryMsg(item.inquiry._id)
        })
        this.scrollToBottom()
    }
    componentDidUpdate() {
        this.scrollToBottom();
    }
    scrollToBottom() {
        animateScroll.scrollToBottom({
          containerId: "speechContainer"
        });
    }
    onSendInvoice = () =>{
        let item = this.getCar()
       
        this.props.sendInvoice({
            uid     :this.props.auth.user.id,
            cid     :item.inquiry.car._id,
            invoice :this.state.invoice
        })
    }
    onMessageChange = (event) =>{
        this.setState({
            msg:event.target.value
        })
    }
    sendMsg = () =>{
        let item = this.getCar()
        console.log(item)
        if(item.inquiry.state != 2)
        {
            alert(`You must ask Invoice first!${item.inquiry.state}`)
            return
        }
        if(this.state.msg != ""){
            this.setState({
                msg:""
            })
            let item = this.getCar()
            console.log('sendInvoiceMSg')
            this.props.socket.emit('sendInvoiceMsg',{
                inquiry:item.inquiry._id,
                user:this.props.auth.user,
                msg:this.state.msg,
                car:item.inquiry.car._id
            })
        }
        
    }
    getCar = () =>{
        const item = this.props.inquiry.currentInvoice
        return item
    }
    render(){

        let item = this.getCar()
        const imgpath = item.inquiry.car.images.length == 0 ? "":item.inquiry.car.images[0]
        return(
            <div className="InquiryContent">
                <Row className="InquiryContent-Header">
                    <a onClick={()=>{this.props.toggle(null)}} style={{cursor:'pointer',padding:'10px'}}> Back to Inquiry List</a>
                </Row>
                <Row className="InquiryContent-Content">
                    <Col md="4" className="InquiryContent-Content-First">
                        <div className="InquiryContent-Content-One">
                            <img src={imgpath}></img>
                            <p>{item.inquiry.car.make} {item.inquiry.car.model}</p>
                            <p className="Opacity70">{item.inquiry.car.odometer} m.</p>
                            <p className="Opacity70">{item.inquiry.car.engine_size} {item.inquiry.car.engine_size_unit}</p>
                        </div>
                        <div className="InquiryContent-Content-One">
                            <p>INQUIRY</p>
                            {item.inquiry.state == 2 && (
                                <p>
                                    Invoice sent.
                                </p>
                            )}
                            {item.inquiry.state == 1 && (
                                <Button
                                    variant="contained"
                                    fullWidth
                                    // startIcon={<AddIcon />}
                                    onClick={this.onSendInvoice}
                                    // style={{backgroundColor:"black"}}
                                    className="NormalBtn InquiryContent-Content-Second-Input-Button"
                                >
                                    Ask Invoice
                                </Button>
                            )}
                            
                        </div>
                    </Col>
                    <Col md="8" className="InquiryContent-Content-Second">
                        <div id="speechContainer" className="InquiryContent-Content-Second-Speech" >
                            {Array.isArray(item.chatlogs) && (
                                <>
                                {
                                    item.chatlogs.map((chat,index)=>{
                                        if(chat.sender._id != this.props.auth.user.id){
                                            return(
                                                <div className="Speech-Left Speech-Item">
                                                    <img src="/static/assets/img/avatar1.png" className="Avatar"></img>
                                                    <div className="Speech-Content">
                                                        <p className="Speech-Time">10:46 AM</p>
                                                        <div className="Speech-Detail">
                                                            <div className="Speech-Detail-Title">
                                                                {chat.sender.username}
                                                            </div>
                                                            <div className="Speech-Detail-Content">
                                                                <p>{chat.msgcontent}</p>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            )
                                        }else
                                        {
                                            return(
                                                <div className="Speech-Right Speech-Item">
                                                    <div className="Speech-Content">
                                                        <p className="Speech-Time">10:46 AM</p>
                                                        <div className="Speech-Detail">
                                                            <div className="Speech-Detail-Title">
                                                                Me
                                                            </div>
                                                            <div className="Speech-Detail-Content">
                                                                <p>{chat.msgcontent}</p>
                                                            </div>
                                                        </div>
                                                    </div>
                                                    <img src="./static/assets/img/avatar1.png" className="Avatar"></img>
                                                </div>
                                            )
                                        }
                                        
                                    })
                                }
                                </>
                                )
                            }
                            
                        </div>
                        <div className="InquiryContent-Content-Second-Input" >
                            
                            
                                <TextField 
                                    id="filled-basic" 
                                    label="Invoice" 
                                    fullWidth
                                    variant="outlined"
                                    onChange={this.onMessageChange}
                                    onKeyPress={(e)=>{
                                        if (e.key === 'Enter') {
                                            this.sendMsg()
                                            
                                          }
                                    }}
                                    className="NormalInput InquiryContent-Content-Second-Input-Field"
                                />
                                <Button
                                    variant="contained"
                                    style={{width:'50px',margin:'5px'}}

                                    value={this.state.msg}
                                    onClick={this.sendMsg}
                                    
                                    // style={{backgroundColor:"black"}}
                                    className="NormalBtn InquiryContent-Content-Second-Input-Button"
                                >
                                    Send
                                </Button>
                                              
                        </div>
                    </Col>
                
                </Row>
            </div>
        )
    }
}
const AskInquiryWithSocket = (props) => (
    <SocketContext.Consumer>
      {socket => <AskInquiry {...props} socket={socket} />}
    </SocketContext.Consumer>
  )
const mapStateToProps = (state) =>({
    auth    :state.auth,
    inquiry :state.inquiry,
    //newChatLog:state.global.newChatLog
})

function mapDispatchToProps(dispatch)
{
    return bindActionCreators({ 
      sendInvoice           :InquiryAction.sendInvoice,
      checkDoneInquiryMsg   :InquiryAction.checkDoneInquiryMsg
      //setNewChatLogs  :GlobalAction.setNewChatLogs,

      // showDlg:GlobalAction.showDialog
      //  registerUser :AuthAction.registerUser
    }, dispatch);
}
export default connect(mapStateToProps,mapDispatchToProps)(AskInquiryWithSocket)