import {
    SHOW_DIALOG,
    HIDE_DIALOG,
    SET_CURRENCY,
    SET_SOCKET,
    GLOBAL_GLOBAL_DATA
} from './types'
import api from '../../services/ApiService.js'

export const getGlobalData = () =>dispatch =>{
    api.get('/users/initialdata')
    .then(res=>{
        
        dispatch({
            type:GLOBAL_GLOBAL_DATA,
            payload:res.data
        })
        
    })
}
export const showDialog = (param) => dispatch =>{
    console.log("showDialog")
    dispatch({
        type:SHOW_DIALOG,
        payload:param
    })
}
export const hideDialog = (param) => dispatch =>{
    dispatch({
        type:HIDE_DIALOG,
        payload:param
    })
}
export const setCurrentCurrency = (param) => dispatch=>{
    dispatch({
        type:SET_CURRENCY,
        payload:param
    })
}
export const setSocket = (socket) => dispatch=>{
    dispatch({
        type:SET_SOCKET,
        payload:socket
    })
}