
import React, {Component} from 'react';

import './AdvancedSearch.css';
import { Collapse} from 'reactstrap';
import {withStyles, MuiThemeProvider} from '@material-ui/core/styles';

import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import * as HomeAction from '../../redux/actions/homeAction';

//import {Button,Accordion,AccordionSummary,AccordionDetails} from '@material-ui/core';
import { Row,Col } from 'reactstrap'

import MuiAccordion from '@material-ui/core/Accordion';
import MuiAccordionSummary from '@material-ui/core/AccordionSummary';
import MuiAccordionDetails from '@material-ui/core/AccordionDetails';
import AddIcon from '@material-ui/icons/Add';
import MinusIcon from '@material-ui/icons/Remove';

import TextField from '@material-ui/core/TextField';
import Slider from '@material-ui/core/Slider';

import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';

import Avatar from '@material-ui/core/Avatar';

import MyCheckBox from '../Utility/MyCheckBox'

const bodyFilter = ['Hatchback','Sedan','Coupe','Wagon','SUV','Van']

class PopularBrandList extends Component{
    render(){
        return(
            <div style={{padding:'5px 15px 5px 15px',width:'100%'}}>
                <img style={{width:'20px',height:'20px',marginRight:'10px'}} src={this.props.img}></img>
                {this.props.title}({this.props.count})
                <div style={{float:'right'}}>
                <MyCheckBox {...this.props}/>
                </div>
            </div>
        )
    }
}
class CarBodyType extends Component{
    render(){
        return(
        <div className={this.props.selected ?"CarbodySelected Carbody":"Carbody"} onClick={this.props.toggle}>
                {/* {!this.props.selected && (
                    <div>
                        <div>
                            <img style={{width:'100%',height:'auto',borderRadius:'10px'}} className={this.props.selected ?"CarbodySelected":""} src={this.props.info.img}>
                            </img>
                        </div>
                        <div>
                            {bodyFilter[this.props.index]}
                        </div>
                    </div>
                )}
                {this.props.selected && (
                    <img style={{width:'100%',height:'100%',borderRadius:'10px'}} className={this.props.selected ?"CarbodySelected":""} src={this.props.info.selimg}>
                    </img>
                )}
                {this.props.selected && (
                    <div className="SelectCarBodyBadge">
                        <img src={'./static/assets/img/CHECK.svg'} style={{width:'100%',height:'100%',borderRadius:'50%',marginTop: '-8px'}}></img>
                    </div>
                )} */}
                <div>
                    <img style={{width:'75%',marginLeft:'12%',marginRight:'12%',marginTop:'6%',height:'auto',borderRadius:'10px'}} src={!this.props.selected ? this.props.info.img:this.props.info.selimg}>
                    </img>
                </div>
                <div className={`CarBodyTypeName-${this.props.selected} `}>
                    {/* {this.props.index} */}
                    {bodyFilter[this.props.index]}
                </div>
                {this.props.selected && (
                    <div className="SelectCarBodyBadge">
                        <img src={'./static/assets/img/CHECK.svg'} style={{width:'100%',height:'100%',borderRadius:'50%',marginTop: '-8px'}}></img>
                    </div>
                )}
        </div>
        )
    }
}
const styles = theme => ({
    root: {
        backgroundColor: 'white'
    },
    headerButton: {
        fontSize: '30px'
    },
})
const overrides = {
}
const Accordion = withStyles({
    root: {
       borderBottom: '1px solid rgba(0, 0, 0, 0.2)',
       boxShadow: 'none',
      '&:not(:last-child)': {
        borderBottom: 0,
      },
      '&:before': {
        display: 'none',
      },
      '&$expanded': {
        margin: 'auto',
      },
    },
    borderBottom: '1px solid rgba(0, 0, 0, 0.2)',

    expanded: {},
  })(MuiAccordion);
  
  const AccordionSummary = withStyles({
    root: {
      backgroundColor: 'rgba(0, 0, 0, 0)',
      borderBottom: '1px solid rgba(0, 0, 0, 0.0)',
      marginLeft:10,
      marginRight:10,
      marginBottom: 0,
      minHeight: 56,
      '&$expanded': {
        minHeight: 56,
        borderBottom: '1px solid rgba(0, 0, 0, 0.0)',
      },
    },
    container:{
        borderBottom: '1px solid rgba(0, 0, 0, 0.0)',

    },
    content: {
      '&$expanded': {
        margin: '12px 0',
        height:'60px',
        borderBottom: '1px solid rgba(0, 0, 0, 0.0)',
        
      },
    },
    '$expanded':{
        borderBottom: '1px solid rgba(0, 0, 0, 0.2)',
    }
  })(MuiAccordionSummary);
  
  const AccordionDetails = withStyles((theme) => ({
    root: {
      padding: theme.spacing(2),
          },
  }))(MuiAccordionDetails);
const popularBrandList = [
    {
        img:'./static/assets/img/popularbrand/Mazda.png',
        title:'Mazda'
    },
    {
        img:'./static/assets/img/popularbrand/Toyota.png',
        title:'Toyota'
    },
    {
        img:'./static/assets/img/popularbrand/Honda.png',
        title:'Honda'
    },
    {
        img:'./static/assets/img/popularbrand/Mitubishi.png',
        title:'Mitubishi'
    },
    {
        img:'./static/assets/img/popularbrand/Subaru.png',
        title:'Subaru'
    }
]
const BodyTypeList = [
    {
        img:'./static/assets/img/carbody/Grey/HATCHBACK.svg',

        selimg:'./static/assets/img/carbody/Blue/HATCHBACK1.svg',

        title:'Toyota'
    },
    {
        img:'./static/assets/img/carbody/Grey/SEDAN.svg',

        selimg:'./static/assets/img/carbody/Blue/SEDAN1.svg',

        title:'Honda'
    },
    {
        img:'./static/assets/img/carbody/Grey/COUPE.svg',
        selimg:'./static/assets/img/carbody/Blue/COUPE1.svg',
        title:'Mazda'
    },
    {
        img:'./static/assets/img/carbody/Grey/VAGON.svg',

        selimg:'./static/assets/img/carbody/Blue/VAGON1.svg',

        title:'Subaru'
    },
    
    {
        img:'./static/assets/img/carbody/Grey/SUV.svg',

        selimg:'./static/assets/img/carbody/Blue/SUV1.svg',

        title:'Mitubishi'
    },
    
    {
        img:'./static/assets/img/carbody/Grey/VAN.svg',

        selimg:'./static/assets/img/carbody/Blue/VAN1.svg',

        title:'Subaru'
    }
]
class AdvancedSearch extends Component{

    state={
        expanedList:[false,false,false,false,false,false,false],
        /*
        PopularBrandFilter:[true,true,true,true,true],
        BodyTypeFilter:[true,true,true,true,true,true],
        MileageFilter:[0,0],
        PriceFilter:[500,1500],
        CarUsageFilter:[],
        EngineFilter:[500,1500],

        Transmission:[true,true],
        Drive:[true,true],
        Fuel:[true,true,true,true],
        Steer:[true,true]
        */

    }
    componentWillMount(){
        this.setState({
            ...this.props.filter
        },()=>{
            console.log(this.state)

        })
        //console.log("werwer")
    }
    handleChange = (panel) => (event, isExpanded) => {
        //setExpanded(isExpanded ? panel : false);
        let reExpand = []
        for(let i = 0;i < 7;i++)
        {
            if(i == panel)
            reExpand[i] = isExpanded
            else
            reExpand[i] = false 
        }
        this.setState({
            expanedList:reExpand
        })
        

    };
    selectBrand = (brand) =>{
        let newbrandList = this.state.PopularBrandFilter
        let index = this.state.PopularBrandFilter.indexOf(brand);
        if(index == -1)
        {
            newbrandList.push(brand)
        }else
        {
            newbrandList.splice(index,1)
        }
        this.setState({
            PopularBrandFilter:newbrandList
        })
        console.log(newbrandList)
    }
    selectCarbody = (body) =>{
        
        let newBodyTypeFilter = this.state.BodyTypeFilter
        let index = this.state.BodyTypeFilter.indexOf(body)
        if(index == -1)
        {
            newBodyTypeFilter.push(body)
        }else
        {
            newBodyTypeFilter.splice(index,1)
        }
        /*
        for(let i = 0;i < 6;i++)
        {
            if(i == index)
            newState[i] = !this.state.BodyTypeFilter[i]
            else
            newState[i] = this.state.BodyTypeFilter[i] 
        }
        */
       console.log(newBodyTypeFilter)
        this.setState({
            BodyTypeFilter:newBodyTypeFilter
        })
    }
    onMileageRangeChanged = (event)=>{
        let newstate = [];
        if(event.target.name == "min")
        {
            newstate.push(event.target.value)
            newstate.push(this.state.MileageFilter[1])

        }else
        {
            newstate.push(this.state.MileageFilter[0])
            newstate.push(event.target.value)
        }
        
        this.setState({
            MileageFilter:newstate
        })
    }
    
    onMileageRangeChangedBySlider = (event,value)=>{
        //  alert(value)
        this.setState({
            MileageFilter:value
        })
      }
    onPriceRangeChanged = (event)=>{
        let newstate = [];
        if(event.target.name == "min")
        {
            newstate.push(parseInt(event.target.value))
            newstate.push(parseInt(this.state.PriceFilter[1]))


        }else
        {
            
            newstate.push(parseInt(this.state.PriceFilter[0]))
            newstate.push(parseInt(event.target.value))
        }
        console.log(newstate)
        this.setState({
            PriceFilter:newstate
        })
    }
    onPriceRangeChangedBySlider = (event,value)=>{
      //  alert(value)
      this.setState({
          PriceFilter:value
      })
    }
    priceText = (value) =>{
        return `${value}$`
    }
    onEngineRangeChanged = (event)=>{
        let newstate = [];
        if(event.target.name == "min")
        {
            newstate.push(parseInt(event.target.value))
            newstate.push(parseInt(this.state.EngineFilter[1]))

        }else
        {
            newstate.push(parseInt(this.state.EngineFilter[0]))
            newstate.push(parseInt(event.target.value))
        }
        
        this.setState({
            EngineFilter:newstate
        })
    }
    onEngineRangeChangedBySlider = (event,value)=>{
        this.setState({
            EngineFilter:value
        })
    }
    EngineText = (value) =>{
        return `${value}$`
    }

    //--transm
    transmissionChange = (index) =>{
        console.log(index)
        let newState = []
        for(let i = 0;i < 2;i++)
        {
            if(i == index)
            newState[i] = !this.state.Transmission[i]
            else
            newState[i] = this.state.Transmission[i] 
        }
        this.setState({
            Transmission:newState
        })
    }
    driveChange = (index) =>{
        console.log(index)
        let newState = []
        for(let i = 0;i < 2;i++)
        {
            if(i == index)
            newState[i] = !this.state.Drive[i]
            else
            newState[i] = this.state.Drive[i] 
        }
        this.setState({
            Drive:newState
        })
    }
    fuelChange = (index) =>{
        console.log(index)
        let newState = []
        for(let i = 0;i < 4;i++)
        {
            if(i == index)
            newState[i] = !this.state.Fuel[i]
            else
            newState[i] = this.state.Fuel[i] 
        }
        this.setState({
            Fuel:newState
        })
    }
    steerChange = (index) =>{
        console.log(index)
        let newState = []
        for(let i = 0;i < 2;i++)
        {
            if(i == index)
            newState[i] = !this.state.Steer[i]
            else
            newState[i] = this.state.Steer[i] 
        }
        this.setState({
            Steer:newState
        })
    }
    setFilter = () => {
        console.log('setfilter')
        this.props.setFilter(this.state)
    }
    render(){
    const {classes} = this.props;
    let brandList = ['Mazda','Toyota','Honda','Mitubishi','Subaru']
    //let bodyFilter = ['Hatchback','Sedan','Coupe','Wagon','SUV','Van']
    //console.log(this.)
    return(
     
            <React.Fragment>
                <div>
                    <Row style={{height:'80px'}}>
                        <Button className="NormalBtn" fullWidth color='primary' variant='outlined' style={{height:'50px'}} onClick={this.setFilter}>SetFilter</Button>
                    </Row>
                    
                    <Paper>
                    <Accordion style={{borderBottom:'1px solid rgba(0,0,0,0.2)'}} onChange={this.handleChange(0)} expanded={this.state.expanedList[0]}>
                        <AccordionSummary expandIcon={this.state.expanedList[0]==true?<MinusIcon/>:<AddIcon />}>
                            Popular Brands
                        </AccordionSummary>
                        <AccordionDetails style={{padding:'5px'}}>
                            <ul style={{width:'100%'}}>
                                {popularBrandList.map((item,index)=>{
                                    let selected = this.state.PopularBrandFilter.indexOf(brandList[index]) == -1 ?false:true
                                    return (
                                        <PopularBrandList key={index} img={item.img} title={item.title} selected={selected} toggle={()=>this.selectBrand(brandList[index])}/>
                                    )

                                })}
                                {/* <PopularBrandList img="./static/assets/img/popularbrand/Mazda.png" title/>
                                <PopularBrandList img="./static/assets/img/popularbrand/Toyota.png"/>
                                <PopularBrandList img="./static/assets/img/popularbrand/Honda.png"/>
                                <PopularBrandList img="./static/assets/img/popularbrand/Mitubishi.png"/>
                                <PopularBrandList img="./static/assets/img/popularbrand/Subaru.png"/> */}

                            </ul>
                        </AccordionDetails>
                    </Accordion>
                    <Accordion style={{borderBottom:'1px solid rgba(0,0,0,0.2)'}} onChange={this.handleChange(1)} expanded={this.state.expanedList[1]}>
                            <AccordionSummary expandIcon={this.state.expanedList[1]==true?<MinusIcon/>:<AddIcon />}>
                            Body Types
                        </AccordionSummary>
                        <AccordionDetails>
                            <div style={{width:'100%'}}>
                            <div style={{display:'flex',justifyContent:'space-around'}}>
                                <CarBodyType index={0} info={BodyTypeList[0]} selected={this.state.BodyTypeFilter.indexOf(bodyFilter[0]) == -1 ? false : true} toggle={()=>this.selectCarbody(bodyFilter[0])}></CarBodyType>
                                <CarBodyType index={1} info={BodyTypeList[1]} selected={this.state.BodyTypeFilter.indexOf(bodyFilter[1]) == -1 ? false : true} toggle={()=>this.selectCarbody(bodyFilter[1])}></CarBodyType>
                            </div>
                            <div style={{display:'flex',justifyContent:'space-around'}}>
                                <CarBodyType index={2} info={BodyTypeList[2]} selected={this.state.BodyTypeFilter.indexOf(bodyFilter[2]) == -1 ? false : true} toggle={()=>this.selectCarbody(bodyFilter[2])}></CarBodyType>
                                <CarBodyType index={3} info={BodyTypeList[3]} selected={this.state.BodyTypeFilter.indexOf(bodyFilter[3]) == -1 ? false : true} toggle={()=>this.selectCarbody(bodyFilter[3])}></CarBodyType>
                            </div>
                            <div style={{display:'flex',justifyContent:'space-around'}}>
                                <CarBodyType index={4} info={BodyTypeList[4]} selected={this.state.BodyTypeFilter.indexOf(bodyFilter[4]) == -1 ? false : true} toggle={()=>this.selectCarbody(bodyFilter[4])}></CarBodyType>
                                <CarBodyType index={5} info={BodyTypeList[5]} selected={this.state.BodyTypeFilter.indexOf(bodyFilter[5]) == -1 ? false : true} toggle={()=>this.selectCarbody(bodyFilter[5])}></CarBodyType>
                            </div>
                            </div>
                        </AccordionDetails>
                    </Accordion>
                    <Accordion style={{borderBottom:'1px solid rgba(0,0,0,0.2)'}} onChange={this.handleChange(2)} expanded={this.state.expanedList[2]}>
                            <AccordionSummary expandIcon={this.state.expanedList[2]==true?<MinusIcon/>:<AddIcon />}>
                            Mileage
                        </AccordionSummary>
                        <AccordionDetails>
                            <div>
                            <div style={{display:'flex',justifyContent:'space-around'}}>
                                <Slider
                                    value={this.state.MileageFilter}

                                    onChange={this.onMileageRangeChangedBySlider}
                                    valueLabelDisplay="auto"
                                    aria-labelledby="range-slider"
                                    getAriaValueText={this.priceText}
                                    max={500000}
                                    min={0}
                                />
                            </div>
                            <div style={{display:'flex',justifyContent:'space-around'}}>
                                <TextField
                                    id="outlined-full-width"
                                    label="Min"
                                    name="min"
                                    style={{ margin: 8 }}
                                    placeholder="Min"
                                    type="number"
                                    fullWidth
                                    margin="normal"
                                    variant="outlined"
                                    value={this.state.MileageFilter[0]}

                                    onChange={this.onMileageRangeChanged}

                                />
                                <TextField
                                    id="outlined-full-width"
                                    label="Max"
                                    name="max"
                                    style={{ margin: 8 }}
                                    placeholder="Max"
                                    type="number"
                                    fullWidth
                                    margin="normal"
                                    variant="outlined"
                                    value={this.state.MileageFilter[1]}

                                    onChange={this.onMileageRangeChanged}
                                />
                            </div>
                            </div>
                        </AccordionDetails>
                    </Accordion>
                    <Accordion style={{borderBottom:'1px solid rgba(0,0,0,0.2)'}} onChange={this.handleChange(3)} expanded={this.state.expanedList[3]}>
                        <AccordionSummary expandIcon={this.state.expanedList[3]==true?<MinusIcon/>:<AddIcon />}>
                            Price
                        </AccordionSummary>
                        <AccordionDetails>
                            <div>
                                <div style={{display:'flex',justifyContent:'space-around'}}>
                                    <Slider
                                        value={this.state.PriceFilter}

                                        onChange={this.onPriceRangeChangedBySlider}
                                        valueLabelDisplay="auto"
                                        aria-labelledby="range-slider"
                                        getAriaValueText={this.priceText}
                                        max={50000}
                                        min={0}
                                    />
                                </div>
                                <div style={{display:'flex',justifyContent:'space-around'}}>
                                    <TextField
                                        id="outlined-full-width"
                                        label="Min"
                                        name="min"
                                        style={{ margin: 8 }}
                                        placeholder="Min"
                                        type="number"
                                        fullWidth
                                        margin="normal"
                                        variant="outlined"
                                        value={this.state.PriceFilter[0]}
                                        onChange={this.onPriceRangeChanged}

                                    />
                                    <TextField
                                        id="outlined-full-width"
                                        label="Max"
                                        name="max"
                                        style={{ margin: 8 }}
                                        placeholder="Max"
                                        type="number"
                                        fullWidth
                                        margin="normal"
                                        variant="outlined"
                                        value={this.state.PriceFilter[1]}

                                        onChange={this.onPriceRangeChanged}
                                    />
                                </div>
                            </div>
                        </AccordionDetails>
                    </Accordion>
                    <Accordion style={{borderBottom:'1px solid rgba(0,0,0,0.2)'}} onChange={this.handleChange(4)} expanded={this.state.expanedList[4]}>
                        <AccordionSummary expandIcon={this.state.expanedList[4]==true?<MinusIcon/>:<AddIcon />}>
                            Car Usage
                        </AccordionSummary>
                        <AccordionDetails>
                    
                        </AccordionDetails>
                    </Accordion>
                    <Accordion style={{borderBottom:'1px solid rgba(0,0,0,0.2)'}} onChange={this.handleChange(5)} expanded={this.state.expanedList[5]}>
                        <AccordionSummary expandIcon={this.state.expanedList[5]==true?<MinusIcon/>:<AddIcon />}>
                            Engine
                        </AccordionSummary>
                        <AccordionDetails>
                            <div>
                                <div style={{display:'flex',justifyContent:'space-around'}}>
                                    <Slider
                                        //defaultValue={this.state.EngineFilter}
                                        value={this.state.EngineFilter}
                                        onChange={this.onEngineRangeChangedBySlider}
                                        valueLabelDisplay="auto"
                                        aria-labelledby="range-slider"
                                        getAriaValueText={this.priceText}
                                        max={7000}
                                        min={0}
                                    />
                                </div>
                                <div style={{display:'flex',justifyContent:'space-around'}}>
                                    <TextField
                                        id="outlined-full-width"
                                        label="Min"
                                        name="min"
                                        style={{ margin: 8 }}
                                        placeholder="Min"
                                        type="number"
                                        fullWidth
                                        margin="normal"
                                        variant="outlined"
                                        value={this.state.EngineFilter[0]}
                                        onChange={this.onEngineRangeChanged}

                                    />
                                    <TextField
                                        id="outlined-full-width"
                                        label="Max"
                                        name="max"
                                        style={{ margin: 8 }}
                                        placeholder="Max"
                                        type="number"
                                        fullWidth
                                        margin="normal"
                                        variant="outlined"
                                        value={this.state.EngineFilter[1]}

                                        onChange={this.onEngineRangeChanged}
                                    />
                                </div>
                            </div>
                        </AccordionDetails>
                    </Accordion>
                    </Paper>
                </div>
                <Paper style={{marginTop:'30px'}}>                            
                    <Accordion onChange={this.handleChange(6)} expanded={this.state.expanedList[6]}>
                        <AccordionSummary expandIcon={this.state.expanedList[6]==true?<MinusIcon/>:<AddIcon />}>
                            Filters
                        </AccordionSummary>
                        <AccordionDetails>
                            <div style={{width:'100%'}}>
                                <div className="FilterSection">
                                    <div className="FilterSectionHeader">
                                        Transmission:
                                    </div>
                                    <div style={{display:'flex',justifyContent:'space-around'}}>
                                        <div style={{display:'flex',justifyContent:'space-around'}}>
                                            <div style={{marginRight:'10px'}}>AT</div>
                                            <div style={{float:'right'}}>
                                                <MyCheckBox toggle={() => {this.transmissionChange(0)}} selected={this.state.Transmission[0]}/>
                                            </div>
                                        </div>
                                        <div style={{display:'flex',justifyContent:'space-around'}}>
                                            <div style={{marginRight:'10px',marginLeft:'10px'}}>MT</div><MyCheckBox toggle={() => {this.transmissionChange(1)}} selected={this.state.Transmission[1]}/>
                                        </div>
                                    </div>
                                </div>
                                <div className="FilterSection">
                                    <div className="FilterSectionHeader">
                                        Drive:
                                    </div>
                                    <div style={{display:'flex',justifyContent:'space-around'}}>
                                        <div style={{display:'flex',justifyContent:'space-around'}}>
                                            <div style={{marginRight:'10px'}}>2WD</div> <MyCheckBox toggle={() => {this.driveChange(0)}} selected={this.state.Drive[0]}/>
                                        </div>
                                        <div style={{display:'flex',justifyContent:'space-around'}}>
                                            <div style={{marginRight:'10px',marginLeft:'10px'}}>4WD</div><MyCheckBox toggle={() => {this.driveChange(1)}} selected={this.state.Drive[1]}/>
                                        </div>
                                    </div>
                                </div>
                                <div className="FilterSection">
                                    <div className="FilterSectionHeader">
                                        Fuel:
                                    </div>
                                    <div style={{display:'flex',justifyContent:'space-around'}}>
                                        <div style={{display:'flex',justifyContent:'space-around'}}>
                                            <div style={{marginRight:'10px'}}>Petrol</div> <MyCheckBox toggle={() => {this.fuelChange(0)}} selected={this.state.Fuel[0]}/>
                                        </div>
                                        <div style={{display:'flex',justifyContent:'space-around'}}>
                                            <div style={{marginRight:'10px',marginLeft:'10px'}}>Disel</div><MyCheckBox toggle={() => {this.fuelChange(1)}} selected={this.state.Fuel[1]}/>
                                        </div>
                                    </div>
                                    <div style={{display:'flex',justifyContent:'space-around',marginTop:'5px'}}>
                                        <div style={{display:'flex',justifyContent:'space-around'}}>
                                            <div style={{marginRight:'10px'}}>Hybrid</div> <MyCheckBox toggle={() => {this.fuelChange(2)}} selected={this.state.Fuel[2]}/>
                                        </div>
                                        <div style={{display:'flex',justifyContent:'space-around'}}>
                                            <div style={{marginRight:'10px',marginLeft:'10px'}}>Electric</div><MyCheckBox toggle={() => {this.fuelChange(3)}} selected={this.state.Fuel[3]}/>
                                        </div>
                                    </div>
                                </div>
                                <div className="FilterSection">
                                    <div className="FilterSectionHeader">
                                        Steering:
                                    </div>
                                    <div style={{display:'flex',justifyContent:'space-around'}}>
                                        <div style={{display:'flex',justifyContent:'space-around'}}>
                                            <div style={{marginRight:'10px'}}>Left</div> <MyCheckBox toggle={() => {this.steerChange(0)}} selected={this.state.Steer[0]}/>
                                        </div>
                                        <div style={{display:'flex',justifyContent:'space-around'}}>
                                            <div style={{marginRight:'10px',marginLeft:'10px'}}>Right</div><MyCheckBox toggle={() => {this.steerChange(1)}} selected={this.state.Steer[1]}/>
                                        </div>
                                    </div>
                                </div>
                            </div>

                        </AccordionDetails>
                    </Accordion>
                </Paper>
               </React.Fragment>
       
    )
   }
}
const mapStateToProps = (state) =>({
    filter:state.home.advancedfilter,
 //   sortBy:state.home.sortBy
})

function mapDispatchToProps(dispatch)
{
    return bindActionCreators({      
        setFilter :HomeAction.setFilter
    }, dispatch);
}
export default connect(mapStateToProps,mapDispatchToProps)((withStyles(styles, {withThemem: true})(AdvancedSearch)))