import React, {Component} from 'react';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import {Link} from 'react-router-dom';

import * as DetailProductAction from '../../redux/actions/detailProductAction';
import * as HomeAction from '../../redux/actions/homeAction';
import * as GlobalAction from '../../redux/actions/globalAction';

import {withStyles, MuiThemeProvider} from '@material-ui/core/styles';

import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Typography from '@material-ui/core/Typography'
import Button from '@material-ui/core/Button';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';

import FavoriteBorderOutlinedIcon from '@material-ui/icons/FavoriteBorderOutlined';
import FavoriteOutlinedIcon from '@material-ui/icons/FavoriteOutlined';

import DialogComponent from '../DialogComponent';

const styles = theme => ({
    root: {
        //maxWidth: 345,
      },
      media: {
        height: 140,
      }
})
class CarInfoItem extends Component{
    
    componentDidMount(){
    
    }
    componentWillReceiveProps(nextProps)
    {
      
    }
    onDetail = ()=>{

      
        this.props.setDetailProduct(this.props.info)
    }
    setFavorite = (e) =>{
        
        e.preventDefault()
        if(!this.props.isfavorite && !this.props.auth.isAuthenticated && this.props.favCnt > 3){
            this.props.showDlg({
                type:'fail',
                title:'Failed',
                msg :'Please signin'
            })
        }else
        {
            this.props.setFavorite({
                carid:this.props.info._id,
             //   uid:this.props.auth.user.id
            })
        }
        
      
    }
    render(){
        const {classes,info,isfavorite} = this.props
        
        
       // let isfavorite = this.props.favorite.indexOf(this.props.info._id) == -1 ? false:true
        return(
            <>
            <Link to={{
                pathname:`/detail/${info._id}`,
                
            }} style={{width:'100%',marginBottom:'20px',display:"flex",justifyContent:'center'}}>
                <Card className={classes.root} fullWidth style={{width:'100%',height:'auto',position:'relative'}} onClick={this.onDetail}>
                    <CardActionArea>
                        <CardMedia
                            className={classes.media}
                            image={info.images[0]}
                            title={info.description}
                        />

                        <CardContent>
                        <div style={{
                            display: 'flex',
                            justifyContent:"space-between"
                        }}>
                            <div>
                                <Typography gutterBottom variant="h5" component="h2">
                                    {info.make} {info.model}
                                </Typography>
                                <Typography gutterBottom variant="h6" style={{fontSize:'18px'}} component="h2">
                                    {info.build_year}
                                </Typography>
                                
                                </div>
                            <div>
                                <Typography gutterBottom variant="h6" style={{fontSize:'18px'}} component="h2">
                                    {this.props.currency == 'AUD' && (
                                        <>
                                            {info.price[0].AUD} AUD
                                        </>
                                    )}
                                    {this.props.currency == 'USD' && (
                                        <>
                                            {info.price[0].USD} USD
                                        </>
                                    )}
                                    {this.props.currency == 'EUR' && (
                                        <>
                                            {info.price[0].EUR} EUR
                                        </>
                                    )}
                                </Typography>
                                
                            </div>
                        </div>
                        <Divider />
                        <div style={{
                            marginTop:'15px',
                            display: 'flex',
                            justifyContent:"space-between"
                        }}>
                            <div>
                                <p>Engine</p>
                                {info.engine_size}{info.engine_size_unit}
                            </div>
                            <div>
                                <p>Miles</p>
                                {info.odometer}
                            </div>
                            <div>
                                <p>Shipment Price</p>
                                {info.engine_size}{info.engine_size_unit}
                            </div>
                        </div>
                        
                        
                        </CardContent>
                    </CardActionArea>
                    <IconButton style={{
                        position:'absolute',
                        right:'5px',
                        top:'5px'
                    }} color="primary" aria-label="upload picture" component="span"  onClick={this.setFavorite} >
                        {isfavorite && (
                            <FavoriteOutlinedIcon/>
                        )}
                        {!isfavorite && (
                            <FavoriteBorderOutlinedIcon/>
                        )}
                        
                    </IconButton>
                </Card>
                
            </Link>
            
            </>

        )
    }
}
const mapStateToProps = (state) =>({
    //detailproduct:state.auth
  //  favorite:state.home.favorite,
    favCnt      :state.home.favCnt,
    auth        :state.auth
})

function mapDispatchToProps(dispatch)
{
    return bindActionCreators({
        setFavorite         :HomeAction.setFavorite,
        setDetailProduct    :DetailProductAction.setDetailProduct,
        showDlg             :GlobalAction.showDialog
    }, dispatch);
}
export default connect(mapStateToProps,mapDispatchToProps)((withStyles(styles, {withThemem: true})(CarInfoItem))); 