import {
    TOGGLE_SIDE_MENU,
    ADMIN_SET_TITLE,
    GET_INVOICE_LIST,
    GET_ASK_INVOICE_CAR,
    NEW_INVOICE_MSG,
    ADMIN_CHECK_DONE_INQUIRY_MSG,
    ADMIN_ACCEPT_INVOICE,
    ADMIN_ACCEPT_NEXTSTEP,
    ADMIN_CHANGE_ORDER_INFO,
    GET_ORDER_INVOICE_CAR,
    ADMIN_UPLOAD_DOC,
    ADMIN_DELETE_DOC,
    ADMIN_UPDATE_DOC,
    ADMIN_EDIT_DOC,
    // ADMIN_GET_ALL_CARS

 } from './types'
import api from '../../services/ApiService.js'

export const toggleAdminSideBar = () => dispatch =>{
    dispatch({
        type:TOGGLE_SIDE_MENU
    })
}
export const setTitle = (title) => dispatch =>{
    dispatch({
        type:ADMIN_SET_TITLE,
        payload:title
    })
}
export const getInvoiceList = () => dispatch=>{
    
    api.get('/admin/invoice',{

    })
    .then(res=>{
        dispatch({
            type:GET_INVOICE_LIST,
            payload:res.data
        })
    })
    
}

export const addInvoiceMsg = (chatlog) => dispatch=>{
    console.log('chatlog')
    console.log(chatlog)
    dispatch({
        type:NEW_INVOICE_MSG,
        payload:chatlog
    })
}
export const checkDoneInquiryMsg = (inquiryId) => dispatch=>{
    dispatch({
        type:ADMIN_CHECK_DONE_INQUIRY_MSG,
        payload:inquiryId
    })
}
export const acceptInvoice = (param) => dispatch=>{
    api.post('/admin/accpetInvoice',{
        param
    })
    .then(res=>{
        console.log('accept')
        console.log(res.data)
        dispatch({
            type:ADMIN_ACCEPT_INVOICE,
            payload:res.data
        })
        
    })
}
export const getOrderInvoice = (param) => dispatch=>{
    api.get('/admin/orders',{
    })
    .then(res=>{
        dispatch({
            type:GET_ORDER_INVOICE_CAR,
            payload:res.data.orders
        })
    })
}
export const uploadDoc = (param) => dispatch =>{
    api.post('/admin/uploadDoc',{
        param
    })
    .then(res=>{
        dispatch({
            type:ADMIN_UPDATE_DOC,
            payload:res.data
        })
    })
}
export const deleteDoc = (param) => dispatch =>{
    api.post('/admin/deleteDoc',{
        param
    })
    .then(res=>{
        dispatch({
            type:ADMIN_UPDATE_DOC,
            payload:res.data
        })
    })
}
export const editDoc = (param) => dispatch =>{
    api.post('/admin/editDoc',{
        param
    })
    .then(res=>{
        dispatch({
            type:ADMIN_UPDATE_DOC,
            payload:res.data
        })
    })
}
export const accpetStep = (param) => dispatch =>{
    api.post('/admin/accpetStep',{
        param
    })
    .then(res=>{
        if(res.data.result == true){
            dispatch({
                type:ADMIN_CHANGE_ORDER_INFO,
                payload:res.data
            })
        }
    })
}
export const cancelOrder = (param) => dispatch=>{
    api.post('/admin/changeOrderState',{
        param
    })
    .then(res=>{
        if(res.data.result == true){
            dispatch({
                type:ADMIN_CHANGE_ORDER_INFO,
                payload:res.data
            })
        }
    })
}
export const refreshFlow = (param) => dispatch=>{
    api.get('/inquiry/refreshFlowchart',{
        params:param
    })
    .then(res=>{
            dispatch({
                type:ADMIN_CHANGE_ORDER_INFO,
                payload:res.data
            })
        
    })
}
