const {
    USER_LOADING, 
    USER_LOADED, 
    AUTH_ERRORS, 
    LOGIN_SUCCESS,
    SET_CURRENT_USER, 
    LOGIN_FAIL,
    REGISTER_START, 
    REGISTER_DONE, 
    REGISTER_FAIL, 
    LOGOUT_SUCCESS,
    AUTH_CHANGE_USER
} = require('../actions/types');
//const isEmpty = require("is-empty");

const initialState = {
    token : localStorage.getItem("CFAToken"),
    isAuthenticated : false,
    isLoading : false,
    user : null,
    regResult:0
}

export default function(state = initialState, action){

    switch(action.type){

        case USER_LOADING:
            return {
                ...state,
                isLoading : true
            }

        case USER_LOADED:
            return {
                ...state,
                isAuthenticated : true,
                isLoading : false,
                user : action.payload.user
            }
        case SET_CURRENT_USER:
            {
           //     console.log("SETCURRENT")
                let authenti = action.payload == null? false:true;
           //     console.log(action.payload)
           //     console.log(authenti)
                return {
                    ...state,
                    isAuthenticated: authenti,
                    user: action.payload
                }   
            }
        case LOGIN_SUCCESS:
            {
                console.log('login')
                return {
                    ...state,
                    token : action.payload.token,
                    isAuthenticated:true,
                    user:action.user
                }
            }

        case REGISTER_DONE:
            return {
                ...state,
                regResult:action.payload
            }
        case REGISTER_START:
            return {
                ...state,
                regResult:1
            }
        case LOGIN_FAIL:
        case LOGOUT_SUCCESS:
        case REGISTER_FAIL:
        case AUTH_ERRORS:
            return {
                ...state,
                token : null,
                user : null,
                isAuthenticated : false,
                isLoading : false

            }
        case AUTH_CHANGE_USER:
            {
                let newuser = action.payload.user
                return {
                    ...state,
                    user:newuser
                }
            }

        default:
            return state
    }
}