import React, {Component} from 'react';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import { animateScroll } from "react-scroll";

import * as AdminAction from '../../redux/actions/adminAction';

import SocketContext from '../../socket-context'

import { Button , TextField } from '@material-ui/core'

import './Admin.css'
import {BASE_URL} from '../../services/ApiService'

class AdminChatComponent extends Component{
    state={
        msg:''
    }
    componentDidMount(){
        this.props.socket.emit('checkDoneInquiryMsg',{
            inquiry:this.props.currentInquiry.inquiry._id,
            receiver:this.props.auth.user.id
        })
        this.props.socket.on('__sendInvoiceMsg',this.onSendInvoiceMsg)
        this.props.socket.on('__checkDoneInquiryMsg',this.onCheckDoneInquiryMsg)
        this.scrollToBottom()
    }
    componentDidUpdate() {
        this.scrollToBottom();
    }
    componentWillUnmount(){
        this.props.socket.off('__sendInvoiceMsg',this.onSendInvoiceMsg)
        this.props.socket.off('__checkDoneInquiryMsg',this.onCheckDoneInquiryMsg)

    }
    scrollToBottom() {
        animateScroll.scrollToBottom({
          containerId: "speechContainer"
        });
    }
    //socket
    onSendInvoiceMsg = (data) =>{

        this.props.socket.emit('checkDoneInquiryMsg',{
            inquiry:this.props.currentInquiry.inquiry._id,
            receiver:this.props.auth.user.id
        })
    }
    onCheckDoneInquiryMsg = (data) =>{
        this.props.checkDoneInquiryMsg(this.props.currentInquiry.inquiry._id)
    }
    //msg
    onMessageChange = (event) =>{
        this.setState({
            msg:event.target.value
        })
    }
    sendmsg = () =>{
        const { currentInquiry } = this.props
        console.log(currentInquiry)
        if(this.state.msg != ""){

            this.props.socket.emit('sendInvoiceMsgFromAdmin',{
                inquiry     :currentInquiry.inquiry._id,
                sender      :this.props.auth.user.id,
                receiver    :currentInquiry.inquiry.user._id,
                msgcontent  :this.state.msg,
                car         :currentInquiry.inquiry.car._id
            })
            this.setState({
                msg:""
            })
        }
        
    }
    render(){
        const { currentInquiry } = this.props
        return(
            <>
                {
                    currentInquiry && Array.isArray(currentInquiry.chatlogs) && (
                        <div id="speechContainer"  className="InvoiceSpeech">
                        {currentInquiry.chatlogs.map((chat,index)=>{

                            if(chat.sender._id != this.props.auth.user.id){
                                return(
                                    <div className="Speech-Left Speech-Item">
                                        {/* <img src="/static/assets/img/avatar1.png" className="Avatar"></img> */}
                                        <img className="Avatar" src={chat.sender.avatar ? `${BASE_URL}/upload/avatar/${chat.sender.avatar}` : `${BASE_URL}/upload/avatar/default.png`}></img>

                                        <div className="Speech-Content">
                                            <p className="Speech-Time">10:46 AM</p>
                                            <div className="Speech-Detail">
                                                <div className="Speech-Detail-Title">
                                                    {chat.sender.username}
                                                </div>
                                                <div className="Speech-Detail-Content">
                                                    <p>{chat.msgcontent}</p>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                )
                            }else
                            {
                                return(
                                    <div className="Speech-Right Speech-Item">
                                        <div className="Speech-Content">
                                            <p className="Speech-Time">10:46 AM</p>
                                            <div className="Speech-Detail">
                                                <div className="Speech-Detail-Title">
                                                    Me
                                                </div>
                                                <div className="Speech-Detail-Content">
                                                    <p>{chat.msgcontent}</p>
                                                </div>
                                            </div>
                                        </div>
                                        {/* <img src="/static/assets/img/avatar1.png" className="Avatar"></img> */}
                                        <img className="Avatar" src={this.props.auth.user.avatar ? `${BASE_URL}/upload/avatar/${this.props.auth.user.avatar}` : `${BASE_URL}/upload/avatar/default.png`}></img>

                                    </div>
                                )
                            }
                        })}
                        </div>
                    )
                }
                <div style={{margin:'20px',display:'flex',justifyContent:'space-around'}}>
                    <TextField 
                            id="filled-basic" 
                            label="Message" 
                            variant="outlined"
                            value={this.state.msg}

                            onKeyPress={(e)=>{
                                if (e.key === 'Enter') {
                                    if(this.state.msg != ''){
                                        this.setState({msg:''})
                                        this.sendmsg()
                                    }
                                  }
                            }}
                            fullWidth
                            onChange={this.onMessageChange}
                            className="NormalInput"
                        />
                        <Button
                            style={{width:'50px',margin:'5px'}}
                            variant="contained"
                            onClick={()=>{
                                if(this.state.msg != ''){
                                    this.setState({msg:''})
                                    this.sendmsg()
                                }
                            }}
                            
                            className="NormalBtn"
                        >
                            Send
                        </Button>
                </div>
            </>
        )
    }
}
const AdminChatComponentWithSocket = (props) => (
    <SocketContext.Consumer>
      {socket => <AdminChatComponent {...props} socket={socket} />}
    </SocketContext.Consumer>
  )
const mapStateToProps = (state) =>({
    auth    :state.auth,
    admin :state.admin
})

function mapDispatchToProps(dispatch)
{
    return bindActionCreators({ 
        checkDoneInquiryMsg   :AdminAction.checkDoneInquiryMsg
    }, dispatch);
}
export default connect(mapStateToProps,mapDispatchToProps)(AdminChatComponentWithSocket)