import {
    USER_LOADING, 
    USER_LOADED, 
    AUTH_ERRORS,
    SET_CURRENT_USER, 
    LOGIN_SUCCESS, 
    LOGIN_FAIL,
    REGISTER_START, 
    REGISTER_DONE,
    SHOW_DIALOG,
    LOUGOUT_USER, 
    LOGOUT_SUCCESS,
    AUTH_CHANGE_USER
} from './types'
import api from '../../services/ApiService.js'
import * as GlobalAction from './globalAction'
import setAuthToken from "../../utils/setAuthToken";
import jwt_decode from "jwt-decode";

export const registerUser = (param) => dispatch =>{
    console.log('register')
    dispatch(loading());

    api.post('/auth/register',{param})
    .then(res => {
        console.log(res.data)
        if(res.data.success == 3){
            dispatch({
                type:SHOW_DIALOG,
                payload:{
                    type:'success',
                    title:'SignUp Success!',
                    msg:'SignUp Success!'
                }
            })
        }else
        {
            dispatch({
                type:SHOW_DIALOG,
                payload:{
                    type:'fail',
                    title:'SignUp Failed!',
                    msg:'Your email  is already exist!'
                }
            })
        }
        dispatch({
            type:REGISTER_DONE,
            payload:res.data.success
        })
    })
}
