import React from 'react';
import {BrowserRouter, Route, Switch} from 'react-router-dom';
import Home from './pages/Home';
import UserRecipes from './pages/UserRecipes';
import jwt_decode from "jwt-decode";

// Redux stuff
import {Provider} from 'react-redux';
import store from './redux/store';
import AuthLayout from './layout/AuthLayout'
import DefaultLayout from './layout/DefaultLayout'
import AdminLayout from './layout/AdminLayout'

import './App.css';
import CarInfoDetail from './pages/CarInfoDetail/CarInfoDetail'
import SignupComponent from './pages/Authentication/SignupComponent';
import SigninComponent from './pages/Authentication/SigninComponent';
import setAuthToken from "./utils/setAuthToken";

import { setCurrentUser, logoutUser } from "./redux/actions/authAction";
import {Col, Container, Row} from 'reactstrap'
import Navbar from './components/Navbar/Navbar';

import DialogComponent from './components/DialogComponent';
import SocketContext from './socket-context'
import {BASE_URL} from './services/ApiService'
import io from 'socket.io-client';


if (localStorage.CFAToken) {
  // Set auth token header auth
  const token = localStorage.CFAToken;
  setAuthToken(token);
  // Decode token and get user info and exp
  const decoded = jwt_decode(token);
  // Set user and isAuthenticated
  store.dispatch(setCurrentUser(decoded));
  const currentTime = Date.now() / 1000; // to get in milliseconds
  if (decoded.exp < currentTime) {
    store.dispatch(logoutUser());
    window.location.href = "./";
  }
}
const socket = io(BASE_URL,{transports: ['websocket']})

function App() {
  return (
    <SocketContext.Provider value={socket}>
    <Provider store = {store}>
    <BrowserRouter>
      <Switch>
          <Route exact path = "/" render={(props) => <DefaultLayout {...props} /> }></Route>
          <Route exact path = "/detail/:id" render={(props) => <DefaultLayout {...props} /> }></Route>
          <Route exact path = "/myinquiry" render={(props) => <DefaultLayout {...props} /> }></Route>

          {/* <Route exact path = "/" component = {Home}></Route>
          <Route exact path = "/detail" component = {CarInfoDetail}></Route> */}

          <Route exact path="/signup" render={(props) => <AuthLayout {...props} /> }/>
          <Route exact path="/signin" render={(props) => <AuthLayout {...props} /> }/>

          <Route exact path="/admin/request" render={(props)=><AdminLayout {...props}/>}></Route>
          <Route exact path="/admin/order" render={(props)=><AdminLayout {...props}/>}></Route>
          <Route exact path="/admin/car" render={(props)=><AdminLayout {...props}/>}></Route>

          <Route exact path="/admin/" render={(props)=><AdminLayout {...props}/>}></Route>


          {/* <Route exact path = "/signup" component = {SignupComponent}></Route>
          <Route exact path = "/signin" component = {SigninComponent}></Route> */}
      </Switch>
      <DialogComponent/>
    </BrowserRouter>
    </Provider>
    </SocketContext.Provider>
  );
}

export default App;
