import React, {Component} from 'react';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import axios from 'axios'; 

import * as AdminAction from '../../../redux/actions/adminAction';
import {Col, Container, Row} from 'reactstrap'
import {DropzoneDialog} from 'material-ui-dropzone'

import { 
    Timeline,
    TimelineItem,
    TimelineSeparator,
    TimelineConnector,
    TimelineContent,
    TimelineOppositeContent,
    TimelineDot,
 } from '@material-ui/lab';

import { TextField , Button, Chip }  from '@material-ui/core';
import { Paper , Typography } from '@material-ui/core'

 import DoneIcon from '@material-ui/icons/Done';
import CancelIcon from '@material-ui/icons/Cancel';
import LaptopMacIcon from '@material-ui/icons/LaptopMac';
import SettingsIcon from '@material-ui/icons/Settings';
import NotInterestedIcon from '@material-ui/icons/NotInterested';
import RefreshIcon from '@material-ui/icons/Refresh';

import SocketContext from '../../../socket-context'

import AdminChatComponent from '../AdminChatComponent'
import AdminDocSettingDialog from './AdminDocSettingDialog'
import AdminAcceptSettingDialog from './AdminAcceptSettingDialog'
import AdminInquiryStateSettingDialog from './AdminInquiryStateSettingDialog'
import {BASE_URL} from '../../../services/ApiService'

import '../Admin.css'

const StepTitle = [
    'InvoiceDoc',
    'PaymentProof',
    'Shipment Schedule'
]
const ChipColor = [
    'default',
    'secondary',
    'primary'
]
const DotColor=[
    'default',
    'primary',
]
const resultGroup = [
    {
        text:'OnGoing',
    },
    {
        text:'Done',
    },
    {
        text:'Expired',
    },
    {
        text:'Cancel',
    },
]
class AdminOrderDetail extends Component{

    state={
        showUploadDlg:false,
        currentState:3,
        showDocSetting:false,
        currentEdtDoc:null,
        showAcceptStepSetting:false,
        currentAcceptStep:0,
        showInquiryStateSetting:false,
    }
    componentDidMount(){

    }
    getCurrentInquiry = ()=>{
        let returnVal = null
        console.log(this.props.currentinquiry)
        
        if(Array.isArray(this.props.admin.totalInquiry))
        {
            this.props.admin.totalInquiry.map((ite,index)=>{
                if(ite.inquiry._id == this.props.currentinquiry){
                    returnVal = ite
                }
            })
        }
        console.log(returnVal)
        return returnVal
    }
    /**doc */

    refreshFlowChart = ()=>{
        let currentInquiry = this.getCurrentInquiry()
        this.props.refreshFlow({
            inquiryId:currentInquiry.inquiry._id
        })
    }
    emitRefreshEvent=()=>{
        let currentInquiry = this.getCurrentInquiry()

        this.props.socket.emit('refreshFlowChartFromAdmin',{
            inquiryId:currentInquiry.inquiry._id
        })
    }
    uploadMultiFiles = (files) =>{
        // alert(this.state.currentState)
        // return
        let currentInquiry = this.getCurrentInquiry()
       
        const formData = new FormData()

        for (const file of files) {
            formData.append('files[]', file, file.name);
        }
        const config = {
            headers: {
                'content-type': 'multipart/form-data'
            },
            params:{
                mode:'inquiry',
                inquiryId:currentInquiry.inquiry._id

            }
        }
        axios.post(`${BASE_URL}/api/uploadfile`, formData,config)
        .then(res=>{
            let { files } = res.data
            this.props.uploadDoc({
                inquiryId:currentInquiry.inquiry._id,
                state:this.state.currentState,
                files:files
            })
            
            this.setState({
                showUploadDlg:false
            })
        })
        
    }

    openDoc = (doc)=>{
        //alert(doc.path)
        window.open(`${BASE_URL}/upload/${doc.path}`);
    }
    deleteDoc = (doc) =>{
        //alert("de")
        this.props.deleteDoc({
            doc:doc
        })
    }
    editDocState = (doc)=>{
        
        this.setState({
            currentEdtDoc:doc,
            showDocSetting:true
        })
    }
    onEditDoc = (agree)=>{
        this.props.editDoc({
            doc:this.state.currentEdtDoc,
            state:agree
        })
    }
    /**---doc */
    onAcceptStep = (step) =>{
        let currentInquiry = this.getCurrentInquiry()
        if(currentInquiry.inquiry.state == step)
        {
            this.setState({
                currentAcceptStep:step,
                showAcceptStepSetting:true
            })
        }
    }
    acceptNextStep =()=>{

        let inquiry = this.getCurrentInquiry()
        let docs = null
        let can = true
        if(Array.isArray(inquiry.inquirydocs)){
            docs = inquiry.inquirydocs.filter((doc)=>{
                if(doc.inquiryState == this.state.currentAcceptStep)
                    return true
                else
                    return false
            })
        }
        if(Array.isArray(docs))
        {
            docs.map((doc)=>{
                if(doc.state != 2)
                {
                    can = false
                }
            })
        }
        if(can){
            this.props.accpetStep({
                inquiryId:inquiry.inquiry._id,
                state:this.state.currentAcceptStep + 1
            })
        }else
        {
            //error msg
        }
        
    }
    onOrderResult = () =>{
        let inquiry = this.getCurrentInquiry()
        
        if(inquiry.inquiry.inquiryState == 0){
            this.setState({
                showInquiryStateSetting:true
            })
        }
    }
    cancelOrder = () =>{
        let inquiry = this.getCurrentInquiry()
        this.props.cancelOrder({
            inquiryId:inquiry.inquiry._id,
            inquiryState:3
        })
    }
    render(){
        const item = this.getCurrentInquiry()
        let stateArray = [3,4,5]

        return(
            <>
                <Col md={6}>
                    <Button
                                variant="contained"
                                style={{backgroundColor:'#fc5555!important'}}
                                onClick={this.props.onBack}
                            >
                                Back
                    </Button>
                    <Timeline align="alternate">
                        <TimelineItem>
                            <TimelineOppositeContent>
                            </TimelineOppositeContent>
                            <TimelineSeparator>
                                <TimelineDot color="primary" variant="outlined" style={{cursor:'pointer'}}>
                                            <RefreshIcon onClick={this.refreshFlowChart}/>
                                </TimelineDot>
                                <TimelineConnector />

                            </TimelineSeparator>
                            <TimelineContent>
                            </TimelineContent>
                        </TimelineItem>
                    {
                        stateArray.map((state,index)=>{
                            let passed = false
                            let userHandle = false
                            if(state < item.inquiry.state)
                            {
                                passed = true
                            }
                            if(state == 4)
                            {
                                userHandle = true
                            }
                            let docs = null
                            if(Array.isArray(item.inquirydocs)){
                                docs = item.inquirydocs.filter((doc)=>{
                                    if(doc.inquiryState == state)
                                        return true
                                    else
                                        return false
                                })
                            }
                            return(
                                <TimelineItem>
                                    
                                    <TimelineOppositeContent>
                                        <Paper elevation={3} style={{textAlign:'center'}}>
                                            <div style={{minHeight:'150px',padding:'10px 10px 30px 10px'}}>
                                                
                                                <Typography variant="h6" component="h1">
                                                    {StepTitle[state - 3]}
                                                </Typography>
                                                <div style={{minHeight:'50px'}}>
                                                {Array.isArray(docs) && (
                                                    <>
                                                    {docs.map((doc)=>{
                                                        if(state == 4){
                                                            return(
                                                                <>
                                                                <Chip
                                                                    label={doc.realName}
                                                                    color={`${ChipColor[doc.state]}`}
                                                                    onClick={()=>this.openDoc(doc)}
                                                                    onDelete={()=>this.editDocState(doc)}
                                                                    deleteIcon={doc.state == 0 ? <SettingsIcon/> : doc.state == 1 ? <NotInterestedIcon/> : <DoneIcon/>}
                                                                    variant="outlined"
                                                                    style={{padding:'10px',margin:'5px'}}
                                                                >
                                                                </Chip>                                                 
                                                                </>
                                                            )
                                                        }else
                                                        {
                                                            return(
                                                                <>
                                                                <Chip
                                                                    label={doc.realName}
                                                                    color={`${ChipColor[doc.state]}`}
                                                                    onClick={()=>this.openDoc(doc)}
                                                                    onDelete={()=>
                                                                        this.deleteDoc(doc)
                                                                    }
                                                                    deleteIcon={<CancelIcon/>}
                                                                    variant="outlined"
                                                                    style={{padding:'10px',margin:'5px'}}
                                                                >
                                                                </Chip>
                                                                </>
                                                            )
                                                        }
                                                        
                                                    })}
                                                  
                                                    </>
                                                )}
                                                {docs.length == 0 && (
                                                    <Typography style={{opacity:'.5'}} variant="h5" component="h1">No Doc yet!</Typography>   
                                                )}
                                                </div>
                                                
                                                {
                                                    !userHandle && (//
                                                        <>
                                                        {state <= item.inquiry.state && (
                                                            <Button variant="outlined" fullWidth onClick={()=>{this.setState({
                                                                showUploadDlg:true,
                                                                currentState:state
                                                            })}}>Upload</Button>
                                                        )}
                                                        {state > item.inquiry.state && (
                                                            <Button variant="outlined" disabled fullWidth onClick={()=>{this.setState({showUploadDlg:true})}}>Upload</Button>
                                                        )}
                                                        <DropzoneDialog
                                                            open={this.state.showUploadDlg}
                                                            onSave={(files)=>this.uploadMultiFiles(files)}
                                                            acceptedFiles={['image/jpeg', 'image/png', 'image/bmp', 'image/json' ]}
                                                            showPreviews={true}
                                                            maxFileSize={5000000}
                                                            onClose={()=>{this.setState({showUploadDlg:false})}}
                                                        />
                                                        
                                                        </>
                                                    )
                                                }
                                                
                                                
                                            </div>
                                        </Paper>
                                    </TimelineOppositeContent>
                                    <TimelineSeparator>
                                        <TimelineDot
                                            color={passed?'primary':state==item.inquiry.state ? 'secondary' :''}
                                            onClick={()=>{this.onAcceptStep(state)}}
                                        >
                                            <LaptopMacIcon />
                                        </TimelineDot>
                                        <TimelineConnector />
                                    </TimelineSeparator>
                                    <TimelineContent>
                                        {
                                            passed && (
                                                <>Passed</>
                                            )
                                        }
                                        {
                                            !passed && (
                                                <>NoPassed</>
                                            )
                                        }
                                    </TimelineContent>
                                </TimelineItem>
                            )
                        })
                    }
                        <TimelineItem>
                            <TimelineOppositeContent>
                            </TimelineOppositeContent>
                            <TimelineSeparator>
                                <div className="OrderResult" onClick={this.onOrderResult}>
                                    {resultGroup[item.inquiry.inquiryState].text}
                                </div>
                            </TimelineSeparator>
                            <TimelineContent>
                            </TimelineContent>
                        </TimelineItem>
                                       
                    </Timeline>
                    <AdminDocSettingDialog 
                        doc={this.state.currentEdtDoc} 
                        handleClose={()=>{this.setState({showDocSetting:false})}} 
                        open={this.state.showDocSetting}
                        agree={()=>{
                            this.onEditDoc(2)
                            this.setState({showDocSetting:false})
                        }}
                        disagree={()=>{
                            this.onEditDoc(1)
                            this.setState({showDocSetting:false})
                        }}
                    />
                    <AdminAcceptSettingDialog 
                        handleClose={()=>{this.setState({showAcceptStepSetting:false})}} 
                        open={this.state.showAcceptStepSetting}
                        agree={()=>{
                            this.acceptNextStep()
                            this.setState({showAcceptStepSetting:false})
                        }}
                        disagree={()=>{
                            //this.onEditDoc(1)
                            this.setState({showAcceptStepSetting:false})
                        }}
                     />
                     <AdminInquiryStateSettingDialog
                        handleClose={()=>{this.setState({showInquiryStateSetting:false})}} 
                        open={this.state.showInquiryStateSetting}
                        agree={()=>{
                            this.cancelOrder()
                            this.setState({showInquiryStateSetting:false})
                        }}
                        disagree={()=>{
                            //this.onEditDoc(1)
                            this.setState({showInquiryStateSetting:false})
                        }}
                     />
                    
                </Col>
                <Col md={6}>
                    <AdminChatComponent currentInquiry={item} />
                </Col>
            </>
        )
    }
}
const AdminOrderDetailWithSocket = (props) => (
    <SocketContext.Consumer>
      {socket => <AdminOrderDetail {...props} socket={socket} />}
    </SocketContext.Consumer>
  )
const mapStateToProps = (state) =>({
    auth    :state.auth,
    admin   :state.admin
})

function mapDispatchToProps(dispatch)
{
    return bindActionCreators({
       uploadDoc         :AdminAction.uploadDoc,
       deleteDoc         :AdminAction.deleteDoc,
       editDoc           :AdminAction.editDoc,
    
       accpetStep       :AdminAction.accpetStep,
       cancelOrder      :AdminAction.cancelOrder,
       refreshFlow      :AdminAction.refreshFlow
    }, dispatch);
}
export default connect(mapStateToProps,mapDispatchToProps)(AdminOrderDetailWithSocket)
