import React, {Component} from 'react';
import { ClickAwayListener , Divider } from '@material-ui/core';
import MyCheckBox from './MyCheckBox'

import './MyDropDown.css';
class MyDropDown extends Component{
    state={
        isOpen:false

    }
    componentDidMount() {
        //this.wrapperRef = React.createRef();
        document.addEventListener('mousedown', this.handleClickOutside);
    }

    componentWillUnmount() {
        document.removeEventListener('mousedown', this.handleClickOutside);
    }
    ontoggle = (e) =>{
        this.setState({
            isOpen:!this.state.isOpen
        })
    }
    handleClickAway = () => {
        //setOpen(false);
        this.setState({
            isOpen:false
        })
    };
    onSelectItem = (index)=>{
        this.props.onChange(index,this.props.dropdownlist[index])
        this.setState({
            isOpen:false
        })
    }
    render(){
        const { isOpen } = this.state
        const { isCheck } = this.props
        let show = 'Show'
        return(
            <ClickAwayListener onClickAway={this.handleClickAway}>
                <div className="MyDropDown">
                    <div className={`MyDropDown-Header-${isOpen} Header-Color-${this.props.color} Header-Outline-${this.props.hasoutline} Title-Color-${this.props.color}`} onClick={this.ontoggle}>
                        {
                            this.props.header.hasOwnProperty('firstimage') && (
                                <img className="MyDropDown-Img" src={this.props.header.firstimage}></img>
                            )
                        }
                        <p style={{ whiteSpace: 'nowrap',overflow:'hidden',marginTop:'5px',marginBottom:'5px'}}>
                        {this.props.header.title}
                        </p>
                        <div className="MyDropDown-Header-Drop">
                            {isOpen && (
                                // <img className="MyDropDown-Img" src="./static/assets/img/dropdownopen.png"></img>
                                 <svg x="0px" y="0px" viewBox="0 0 612.011 612.011" style={{fill:`${this.props.color == 'secondary'?'white':'black'}`}}>
                                    <g>
                                        <g>
                                        <path d="M604.501,440.509L325.398,134.956c-5.331-5.357-12.423-7.627-19.386-7.27c-6.989-0.357-14.056,1.913-19.387,7.27     L7.499,440.509c-9.999,10.024-9.999,26.298,0,36.323s26.223,10.024,36.222,0l262.293-287.164L568.28,476.832     c9.999,10.024,26.222,10.024,36.221,0C614.5,466.809,614.5,450.534,604.501,440.509z"/>
                                        </g>
                                    </g>
                                </svg>
                            )}
                            {!isOpen && (
                                // <img className="MyDropDown-Img" src="./static/assets/img/dropdownclose.png"></img>
                                <svg x="0px" y="0px" viewBox="0 0 512.011 512.011" style={{fill:`${this.props.color == 'secondary'?'white':'black'}`}}>
                                    <g>
                                        <g>
                                            <path d="M505.755,123.592c-8.341-8.341-21.824-8.341-30.165,0L256.005,343.176L36.421,123.592c-8.341-8.341-21.824-8.341-30.165,0    s-8.341,21.824,0,30.165l234.667,234.667c4.16,4.16,9.621,6.251,15.083,6.251c5.462,0,10.923-2.091,15.083-6.251l234.667-234.667    C514.096,145.416,514.096,131.933,505.755,123.592z"></path>
                                        </g>
                                    </g>
                                </svg>
                            )}
                        </div>
                    </div>

                    { isOpen && (
                        <div className={`MyDropDown-Content Show`}>
                            {
                                this.props.dropdownlist.map((item,index)=>{
                                    let canCheck = isCheck && item.title === this.props.header.title
                                    return(
                                        <>
                                        <div key={index} className="MyDropDown-Content-Item" onClick={()=>this.onSelectItem(index)}>

                                            {
                                                item.hasOwnProperty('firstimage') && (
                                                    <img className="MyDropDown-Img" src={item.firstimage}></img>
                                                )
                                            }
                                            
                                                {item.title}
                                            { isCheck && (
                                                <div className="Checkbox">
                                                    <MyCheckBox selected={canCheck}></MyCheckBox>
                                                </div>
                                            )}
                                        </div>
                                        <Divider style={{marginLeft:'15px',marginRight:'15px'}}></Divider>
                                        </>
                                    )
                                })
                            }
                            
                        </div>
                    )}
                </div>
            </ClickAwayListener>
        )
    }
}
export default MyDropDown