import React, {Component} from 'react';
import {BrowserRouter, Route, Switch} from 'react-router-dom';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import * as AuthAction from '../redux/actions/authAction';

import {Col, Container, Row} from 'reactstrap'
import AdminSideBar from '../components/Admin/AdminSideBar'
import AdminContentTop from '../components/Admin/AdminContentTop'

import AdminInquiry from '../pages/Admin/AdminInquiry/AdminInquiry';
import AdminOrders from '../pages/Admin/AdminOrders/AdminOrders';
import AdminCarManage from '../pages/Admin/AdminCarManage/AdminCarManage';

import AdminDashboard from '../pages/Admin/AdminDashboard/AdminDashboard'
class AdminLayout extends Component {

    componentDidMount(){
        if ( !this.props.auth.isAuthenticated){    
			this.props.history.push( '/' );
        }else if (this.props.auth.user.name != 'admin') {
			this.props.history.push( '/' );
        }
    }
    render(){
      const { match } = this.props
      return(
        <div className="Admin">
            
            <AdminSideBar></AdminSideBar>
            
            <div className="AdminContent">
                    <AdminContentTop>
                    </AdminContentTop>
                    <div className="AdminContentMain">
                        <Switch>
                            <Route exact path = "/admin/request" component = {AdminInquiry}></Route>
                            <Route exact path = "/admin/order" component = {AdminOrders}></Route>
                            <Route exact path = "/admin/car" component = {AdminCarManage}></Route>

                            <Route exact path = "/admin/" component = {AdminDashboard}></Route>
                        </Switch>
                    </div>
                
            </div>
        </div>
      )
   }

 }
 const mapStateToProps = (state) =>({
    auth:state.auth
})

function mapDispatchToProps(dispatch)
{
    return bindActionCreators({      
    }, dispatch);
}
export default connect(mapStateToProps,mapDispatchToProps)(AdminLayout)