import React, {Component} from 'react';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import * as GlobalAction from '../redux/actions/globalAction'
import {Modal,Typography,Button} from '@material-ui/core';

class DialogComponent extends Component{
    render(){
        return(
            <Modal style={{
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
          }}
              open={this.props.show}
              onClose={()=>{this.props.hide()}}
              aria-labelledby="simple-modal-title"
              aria-describedby="simple-modal-description"
          >
          
              <div style={{
                 
                  width: '25%',
                  minWidth:'250px',
                  backgroundColor: 'white',
                  border: '2px solid #15507B',
                  borderRadius:'10px',
                  padding:'10px',
                  margin:'10px',
                  textAlign:'left',
                  alignItems:'center',
                  textAlign:'center',
                  position:'relative'
                  //padding: theme.spacing(2, 4, 3),
              }}>
                    <div style={{
                        
                        width:'75px',
                        height:'75px',
                        borderRadius:'50%',
                        backgroundColor:'white',
                        position:'absolute',
                        top:'-37px',
                        left:'50%',
                        transform:'translateX(-50%)'
                    }}>
                        <img src={`./static/assets/img/dlg/${this.props.dlgInfo.type}.jpg`} style={{width:'80%',height:'80%',borderRadius:'50%',marginTop:'10%'}}></img>
                    </div> 
                    <div style={{
                        
                        width:'40px',
                        height:'40px',
                        borderRadius:'50%',
                        backgroundColor:'white',
                        position:'absolute',
                        top:'5px',
                        right:'15px'
                        
                    }}>
                         {/* <img src={`./static/assets/img/dlg/${this.props.dlgInfo.type}.jpg`} onClick={()=>{this.props.hide()}} style={{width:'100%',height:'100%',cursor:'hand'}}></img> */}
                    </div> 
                  
                  <Typography style={{width:'100%',color:'black',fontSize:'25px',padding:'20px'}} noWrap={true}>
                      {this.props.dlgInfo.title}
                  </Typography>
                  <Typography style={{width:'100%',color:'black',padding:'20px'}}>
                  {this.props.dlgInfo.msg}
                  </Typography>
                 <Button onClick={()=>{this.props.hide()}} color="primary" style={{backgroundColor:'rgba(73, 154, 236, 1)',margin:'20px'}} variant='contained'>Okay</Button>
              </div>
              
          </Modal>
        )
    }
}
const mapStateToProps = (state) =>({
    show:state.global.showDialog,
    dlgInfo:state.global.dlgInfo,
    
})

function mapDispatchToProps(dispatch)
{
    return bindActionCreators({      
      
        hide :GlobalAction.hideDialog
    }, dispatch);
}


export default connect(mapStateToProps,mapDispatchToProps)(DialogComponent)