const {
    SET_ALL_INQUIRY,
    INQUIRY_SET_CURRENT_PAGE,
    SET_ASK_INVOICEPANEL,
    SEND_INVOICE,
    INQUIRY_CHANGE_ORDER_INFO,
    INQUIRY_UPLOAD_DOC,
    INQUIRY_DELETE_DOC,
    ADD_NEW_INVOICE_MSG,
    CHECK_DONE_INQUIRY_MSG
} = require('../actions/types');

const initialState = {
    page:0,
    currentInvoice:0,
/*  inquiries   :[],
    orders      :[]
    */
   totalInquiry  :[]

}
export default function(state = initialState , action){
    switch(action.type)
    {
        case INQUIRY_SET_CURRENT_PAGE:
            {
                return{
                    ...state,
                    page:action.payload
                }
            }
        case SET_ALL_INQUIRY:
            {
                console.log(action.payload)
                return {
                    ...state,
                    totalInquiry   :action.payload.totalInquiry,
                }
            }
        case SET_ASK_INVOICEPANEL:
            {
                return {
                    ...state,
                    currentInvoice:action.payload
                }
            }

        case SEND_INVOICE:
            {
                let updatedInquiry = action.payload.inquiry
                let inquiryarray = [...state.totalInquiry]
                let index = -1
                inquiryarray.find((o,i)=>{
                   // console.log(o)
                   // console.log(updatedInquiry)

                    if(o.inquiry._id == updatedInquiry.inquiry._id){
                        index = i
                        return true
                    }
                })
                if(index != -1){
                    console.log('updated')
                    inquiryarray[index] = updatedInquiry
                }
                return{
                    ...state,
                    totalInquiry:inquiryarray
                }
            }
        case INQUIRY_CHANGE_ORDER_INFO:
            {
                let updatedInquiry = action.payload.order
                console.log(updatedInquiry)
                let inquiryarray = [...state.totalInquiry]
                let index = -1
                inquiryarray.find((o,i)=>{
                   // console.log(o)
                   // console.log(updatedInquiry)

                    if(o.inquiry._id == updatedInquiry.inquiry._id){
                        index = i
                        return true
                    }
                })
                if(index != -1){
                    console.log('updated')
                    inquiryarray[index] = updatedInquiry
                }
                return{
                    ...state,
                    totalInquiry:inquiryarray
                }
            }
        case INQUIRY_UPLOAD_DOC:
            {
                let updateInquiryId = action.payload.inquiryid
                let inquiryarray = [...state.totalInquiry]
                let index = -1
                let currentInquiry = inquiryarray.find((o,i)=>{
                    if(o.inquiry._id == updateInquiryId){
                         index = i
                         return true
                     }
                })
                if(index != -1){
                    currentInquiry.inquirydocs = action.payload.inquirydocs
                    inquiryarray[index] = currentInquiry
                }
                console.log(inquiryarray[index])
                console.log(state.totalInquiry[index])

                return{
                    ...state,
                    totalInquiry:inquiryarray
                }
            }
        case INQUIRY_DELETE_DOC:
            {
                let updateInquiryId = action.payload.inquiryid
                let inquiryarray = [...state.totalInquiry]
                let index = -1
                let currentInquiry = inquiryarray.find((o,i)=>{
                    if(o.inquiry._id == updateInquiryId){
                         index = i
                         return true
                     }
                })
                if(index != -1){
                    currentInquiry.inquirydocs = action.payload.inquirydocs
                    inquiryarray[index] = currentInquiry
                }
                console.log(inquiryarray[index])
                console.log(state.totalInquiry[index])

                return{
                    ...state,
                    totalInquiry:inquiryarray
                }
            }
        case ADD_NEW_INVOICE_MSG:
            {
                console.log("ADD_NEW_INVOCIE")
                let inquires = [...state.totalInquiry]
                let newchatlog = action.payload
                let currentInquiry = null
                let currentIndex = -1
                currentInquiry = inquires.find((item,index)=>{
                    if(item.inquiry._id == newchatlog.inquiry)
                    {
                        currentIndex = index
                        return true
                    }
                })

                if(currentIndex != -1)
                {
                    currentInquiry.chatlogs.push(newchatlog)
                    inquires[currentIndex] = currentInquiry
                }
                console.log(currentInquiry)
                
                return{
                    ...state,
                    totalInquiry:inquires
                }
                /*
                let inquires = state.inquiries
                let newchatlog = action.payload
                let currentInquiry = null
                let currentIndex = -1
                inquires.find((inquiry,index)=>{
                    if(inquiry.inquiry._id == newchatlog.inquiry)
                    {
                        currentInquiry = inquiry
                        currentIndex = index
                        return true
                    }
                })

                if(currentIndex != -1)
                {
                    currentInquiry.chatlogs.push(newchatlog)
                    inquires[currentIndex] = currentInquiry
                }
                console.log(currentInquiry)
                
                return{
                    ...state,
                    inquiries:inquires
                }
                */
            }
        case CHECK_DONE_INQUIRY_MSG:
            {
                let inquires = [...state.totalInquiry]
                let inquiryId = action.payload
                let currentInquiry = null
                let currentIndex = -1
                currentInquiry = inquires.find((inquiry,index)=>{
                    if(inquiry.inquiry._id == inquiryId)
                    {
                        currentIndex = index
                        return true
                    }
                })
                if(currentIndex != -1)
                {
                    currentInquiry.chatlogs.map((chat,item)=>{
                        chat.state = 0
                    })
                    inquires[currentIndex] = currentInquiry
                }
                return{
                    ...state,
                    totalInquiry:inquires
                }
            }
        default:
            return state
    }
}