import React, {Component} from 'react';

import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import * as AuthAction from '../../redux/actions/authAction';
import * as InquiryAction from '../../redux/actions/inquiryAction';

import SocketContext from '../../socket-context'

import { animateScroll } from "react-scroll";
import {Col, Container, Row} from 'reactstrap'
import { TextField , Button }  from '@material-ui/core';
import "./MyInquiry.css";
import {BASE_URL} from '../../services/ApiService'

class ChatComponent extends Component{

    state={
        msg:'',
    }
    componentDidMount(){

        this.props.socket.emit('checkDoneInquiryMsg',{
            inquiry:this.props.item.inquiry._id,
            receiver:this.props.auth.user.id
        })

        this.props.socket.on('__sendInvoiceMsgFromAdmin',this.onSendInvoiceMsgFromAdmin)
        this.props.socket.on('__successSendInvoiceMsg',this.onSuccessSendInvoiceMsg)
        this.props.socket.on('__checkDoneInquiryMsg',this.onCheckDoneInquiryMsg)
        this.scrollToBottom()
    }
    componentWillUnmount(){
        this.props.socket.off('__sendInvoiceMsgFromAdmin',this.onSendInvoiceMsgFromAdmin)
        this.props.socket.off('__successSendInvoiceMsg',this.onSuccessSendInvoiceMsg)
        this.props.socket.off('__checkDoneInquiryMsg',this.onCheckDoneInquiryMsg)
    }
    //socketfunction
    onSendInvoiceMsgFromAdmin = (data) => {
        
        this.props.socket.emit('checkDoneInquiryMsg',{
            inquiry:this.props.item.inquiry._id,
            receiver:this.props.auth.user.id
        })
    }
    onCheckDoneInquiryMsg = (data) =>{
        this.props.checkDoneInquiryMsg(this.props.item.inquiry._id)
    }
    onSuccessSendInvoiceMsg = (data) =>{
        this.props.addNewInvoiceMsg(data.chatlog)
    }
    //msg
    onMessageChange = (event) =>{
        this.setState({
            msg:event.target.value
        })
    }
    sendMsg = () =>{
        
        const { item } = this.props
        
        if(this.state.msg != ""){
            
            this.props.socket.emit('sendInvoiceMsg',{
                inquiry:item.inquiry._id,
                user:this.props.auth.user,
                msg:this.state.msg,
                car:item.inquiry.car._id
            })
            this.setState({
                msg:""
            })
        }
        
    }
    //scroll
    componentDidUpdate() {
        this.scrollToBottom();
    }
    scrollToBottom() {
        animateScroll.scrollToBottom({
          containerId: "speechContainer"
        });
    }
    render(){
        const { item } = this.props
        
        return(
            <>
                <div id="speechContainer" className="InquiryContent-Content-Second-Speech" >
                    {Array.isArray(item.chatlogs) && (
                        <>
                        {
                            item.chatlogs.map((chat,index)=>{
                                if(chat.sender._id != this.props.auth.user.id){
                                    return(
                                        <div className="Speech-Left Speech-Item">
                                            
                                            <img className="Avatar" src={chat.sender.avatar ? `${BASE_URL}/upload/avatar/${chat.sender.avatar}` : `${BASE_URL}/upload/avatar/default.png`}></img>

                                            <div className="Speech-Content">
                                                <p className="Speech-Time">10:46 AM</p>
                                                <div className="Speech-Detail">
                                                    <div className="Speech-Detail-Title">
                                                        {chat.sender.username}
                                                    </div>
                                                    <div className="Speech-Detail-Content" style={{overflowWrap: 'anywhere'}}>
                                                        <p>{chat.msgcontent}</p>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    )
                                }else
                                {
                                    return(
                                        <div className="Speech-Right Speech-Item">
                                            <div className="Speech-Content">
                                                <p className="Speech-Time">10:46 AM</p>
                                                <div className="Speech-Detail">
                                                    <div className="Speech-Detail-Title">
                                                        Me
                                                    </div>
                                                    <div className="Speech-Detail-Content" style={{overflowWrap: 'anywhere'}}>
                                                        <p>{chat.msgcontent}</p>
                                                    </div>
                                                </div>
                                            </div>
                                            <img className="Avatar" src={this.props.auth.user.avatar ? `${BASE_URL}/upload/avatar/${this.props.auth.user.avatar}` : `${BASE_URL}/upload/avatar/default.png`}></img>

                                        </div>
                                    )
                                }
                                
                            })
                        }
                        </>
                        )
                    }
                    
                </div>
                <div className="InquiryContent-Content-Second-Input" >
                    <TextField 
                        id="filled-basic" 
                        label="Invoice" 
                        fullWidth
                        variant="outlined"
                        value={this.state.msg}
                        onChange={this.onMessageChange}
                        onKeyPress={(e)=>{
                            if (e.key === 'Enter') {
                                    this.sendMsg()
                                
                                }
                        }}
                        className="NormalInput InquiryContent-Content-Second-Input-Field"
                    />
                    <Button
                        variant="contained"
                        style={{width:'50px',margin:'5px'}}
                        onClick={this.sendMsg}
                        className="NormalBtn InquiryContent-Content-Second-Input-Button"
                    >
                        Send
                    </Button>        
                </div>
            </>
        )
        
    }
}
const ChatComponentWithSocket = (props) => (
    <SocketContext.Consumer>
      {socket => <ChatComponent {...props} socket={socket} />}
    </SocketContext.Consumer>
  )
const mapStateToProps = (state) =>({
    auth    :state.auth,
    inquiry :state.inquiry
})

function mapDispatchToProps(dispatch)
{
    return bindActionCreators({ 
        addNewInvoiceMsg            :InquiryAction.addNewInvoiceMsg,
        checkDoneInquiryMsg         :InquiryAction.checkDoneInquiryMsg
    }, dispatch);
}
export default connect(mapStateToProps,mapDispatchToProps)(ChatComponentWithSocket)