import React, {Component} from 'react';

class MyCheckBox extends Component{
    render(){
        return(
            <>
            {this.props.selected && (
                <div style={{width:'20px',height:'20px',borderRadius:'5px',backgroundColor:'rgba(73, 154, 236, 1)'}} onClick={this.props.toggle}>
                    <img style={{width:'100%',height:'100%',marginTop:'-6px'}}  src={'./static/assets/img/CHECK.svg'}></img>
                </div>
            )}
            {!this.props.selected && (
                <div style={{width:'20px',height:'20px',borderRadius:'5px',border:'1px solid grey'}} onClick={this.props.toggle}>

                </div>
            )}
            
            </>
        )
    }
}

export default MyCheckBox