const {
    TOGGLE_SIDE_MENU, 
    ADMIN_SET_TITLE,
    GET_INVOICE_LIST,
    GET_ASK_INVOICE_CAR,
    NEW_INVOICE_MSG,
    ADMIN_CHECK_DONE_INQUIRY_MSG,
    ADMIN_ACCEPT_INVOICE,
    ADMIN_ACCEPT_NEXTSTEP,
    ADMIN_CHANGE_ORDER_INFO,
    GET_ORDER_INVOICE_CAR,
    ADMIN_UPLOAD_DOC,
    ADMIN_DELETE_DOC,
    ADMIN_UPDATE_DOC
} = require('../actions/types');

const initialState = {
    showSideBar :false,
    title:"",
    /*
    askInquires :[],
    orders      :[]
    */
    totalInquiry:[]
}
export default function(state = initialState,action){
    switch(action.type){
        case TOGGLE_SIDE_MENU:
            return{
                ...state,
                showSideBar:!state.showSideBar
            }
        case ADMIN_SET_TITLE:
            return{
                ...state,
                title:action.payload
            }
        case GET_INVOICE_LIST:
            /*
            return{
                ...state,
                askInquires :action.payload.askInquires,
                orders      :action.payload.orders
            }
            */
           return{
               ...state,
               totalInquiry:action.payload.totalInquiry
           }
            
        case GET_ASK_INVOICE_CAR:
            console.log(action.payload)
            console.log("GET_ASK_INVOICE_CAR")
            return{
                ...state,
                askInquires:action.payload
            }
        case NEW_INVOICE_MSG:
            console.log("NEW_INVOICE_MSG")

            let inquires = [...state.totalInquiry]
            let newchatlog = action.payload
            let currentInquiry = null
            let currentIndex = -1
            currentInquiry = inquires.find((inquiry,index)=>{
                if(inquiry.inquiry._id == newchatlog.inquiry)
                {
                    //currentInquiry = inquiry
                    currentIndex = index
                    return true
                }
            })
            if(currentIndex != -1)
            {
                
                let chatId = currentInquiry.chatlogs.indexOf(newchatlog)
                if(chatId == -1){
                    currentInquiry.chatlogs.push(newchatlog)
                }
                inquires[currentIndex] = currentInquiry
            }
            return{
                ...state,
                totalInquiry:inquires
            }
        case ADMIN_CHECK_DONE_INQUIRY_MSG:
            {
            let inquiryId = action.payload

            let inquires = [...state.totalInquiry]
            //let newchatlog = action.payload
            let currentInquiry = null
            let currentIndex = -1
            currentInquiry = inquires.find((inquiry,index)=>{
                if(inquiry.inquiry._id == inquiryId)
                {
                    //currentInquiry = inquiry
                    currentIndex = index
                    return true
                }
            })
            if(currentIndex != -1)
            {
                
                currentInquiry.chatlogs.map((chatlog)=>{
                    chatlog.state = 0
                })
                inquires[currentIndex] = currentInquiry
            }

            return{
                ...state,
                totalInquiry:inquires
            }
        }

        case ADMIN_ACCEPT_INVOICE:
            {
                let updateInquiry = action.payload.order.inquiry
                let total = [...state.totalInquiry]
                let inquirIndex = -1
                total.find((item,index)=>{
                    if(item.inquiry._id == updateInquiry._id){
                        inquirIndex = index
                        return true
                    }
                })
                if(inquirIndex != -1){
                    total[inquirIndex] = action.payload.order
                }
                return{
                    ...state,
                    totalInquiry:total
                }
            }
        case ADMIN_ACCEPT_NEXTSTEP:
            {
                let updateInquiry = action.payload.order.inquiry
                let total = [...state.totalInquiry]
                let inquirIndex = -1
                total.find((item,index)=>{
                    if(item.inquiry._id == updateInquiry._id){
                        inquirIndex = index
                        return true
                    }
                })
                if(inquirIndex != -1){
                    total[inquirIndex] = action.payload.order
                }
                return{
                    ...state,
                    totalInquiry:total
                }
            }
        case ADMIN_CHANGE_ORDER_INFO:
            {
                let updateInquiry = action.payload.order.inquiry
                let total = [...state.totalInquiry]
                let inquirIndex = -1
                total.find((item,index)=>{
                    if(item.inquiry._id == updateInquiry._id){
                        inquirIndex = index
                        return true
                    }
                })
                if(inquirIndex != -1){
                    total[inquirIndex] = action.payload.order
                }
                return{
                    ...state,
                    totalInquiry:total
                }
            }   
        case GET_ORDER_INVOICE_CAR:
            return{
                ...state
            }
        case ADMIN_UPDATE_DOC:
            {
                let updateInquiryId = action.payload.inquiryid
                let inquiryarray = [...state.totalInquiry]
                let index = -1
                let currentInquiry = inquiryarray.find((o,i)=>{
                    if(o.inquiry._id == updateInquiryId){
                            index = i
                            return true
                        }
                })
                if(index != -1){
                    currentInquiry.inquirydocs = action.payload.inquirydocs
                    inquiryarray[index] = currentInquiry
                }
                console.log(inquiryarray[index])
                console.log(state.totalInquiry[index])

                return{
                    ...state,
                    totalInquiry:inquiryarray
                }
            }
            /*
        case ADMIN_DELETE_DOC:
            {
                let updateInquiryId = action.payload.inquiryid
                let inquiryarray = [...state.totalInquiry]
                let index = -1
                let currentInquiry = inquiryarray.find((o,i)=>{
                    if(o.inquiry._id == updateInquiryId){
                         index = i
                         return true
                     }
                })
                if(index != -1){
                    currentInquiry.inquirydocs = action.payload.inquirydocs
                    inquiryarray[index] = currentInquiry
                }
                console.log(inquiryarray[index])
                console.log(state.totalInquiry[index])

                return{
                    ...state,
                    totalInquiry:inquiryarray
                }
            }
            */
        default:
            return state
    }
}