import React, {Component} from 'react';
import {BrowserRouter, Route, Switch} from 'react-router-dom';

import CarInfoDetail from '../pages/CarInfoDetail/CarInfoDetail'
import MyInquiry from '../pages/MyInquiry/MyInquiry'
import Home from '../pages/Home';
import Navbar from '../components/Navbar/Navbar';
import Foot from '../components/Foot/Foot'
import {Col, Container, Row} from 'reactstrap'

class DefaultLayout extends Component {

    render(){
      const { match } = this.props
      return(
        <div>
            <Navbar></Navbar>
            
            <Container fluid={true} style={{backgroundColor:'#f5f6f8',padding:'0px 15px 0px 15px'}}>
                <Switch>
                    {/* <Route 
                        exact path={`${match.path}`}
                        render={(props) => <LoginPage {...props} /> }
                    /> */}
                    <Route exact path = "/detail/:id" component = {CarInfoDetail}></Route>
                    <Route exact path = "/myinquiry" component = {MyInquiry}></Route>
                    <Route exact path = "/" component = {Home}></Route>
                </Switch>
                
            </Container>
            <Foot/>
        </div>
      )
   }

 }

export default DefaultLayout