import {SET_ALL_INQUIRY,
    INQUIRY_SET_CURRENT_PAGE,
    SET_ASK_INVOICEPANEL,
    SEND_INVOICE,
    INQUIRY_CHANGE_ORDER_INFO,
    ADD_NEW_INVOICE_MSG,
    CHECK_DONE_INQUIRY_MSG,
    INQUIRY_UPLOAD_DOC,
    INQUIRY_DELETE_DOC
} from './types'
import api from '../../services/ApiService.js'
import { param } from 'jquery'

export const getAllInquiry = (param) => dispatch => {
    api.get('/inquiry/getInquiryWithUser',{
        params:param
    })
    .then(res=>{
        console.log(res.data)
        dispatch({
            type:SET_ALL_INQUIRY,
            payload:res.data
        })
    })
}
export const setCurrentPage = (index) =>dispatch => {
    dispatch({
        type :INQUIRY_SET_CURRENT_PAGE,
        payload:index
    })
}
export const setAskInvoicePanel = (index) => dispatch =>{
    console.log(index)
    dispatch({
        type:SET_ASK_INVOICEPANEL,
        payload:index
    })
}
export const sendInvoice = (param) => dispatch =>{
    api.post('/inquiry/sendInvocie',{
        param
    })
    .then(res=>{
        dispatch({
            type:SEND_INVOICE,
            payload:res.data
        })
    })
}
export const uploadDoc = (param) => dispatch =>{
    api.post('/inquiry/uploadDoc',{
        param
    })
    .then(res=>{
        dispatch({
            type:INQUIRY_UPLOAD_DOC,
            payload:res.data
        })
    })
}
export const deleteDoc = (param) => dispatch =>{
    api.post('/inquiry/deleteDoc',{
        param
    })
    .then(res=>{
        dispatch({
            type:INQUIRY_DELETE_DOC,
            payload:res.data
        })
    })
}
export const addNewInvoiceMsg = (chatlog) => dispatch =>{
    dispatch({
        type:ADD_NEW_INVOICE_MSG,
        payload:chatlog
    })
}
export const checkDoneInquiryMsg = (inquiryId) => dispatch=>{
    dispatch({
        type:CHECK_DONE_INQUIRY_MSG,
        payload:inquiryId
    })
}
export const refreshFlow = (param) => dispatch=>{
    api.get('/inquiry/refreshFlowchart',{
        params:param
    })
    .then(res=>{
            dispatch({
                type:INQUIRY_CHANGE_ORDER_INFO,
                payload:res.data
            })
        
    })
}