const {SET_DETAIL_PRODUCT,SEND_EMAIL} = require('../actions/types');

const initialState = {
    item:[],
    sendmail:false
}
export default function(state = initialState,action){
    switch(action.type)
    {
        case SET_DETAIL_PRODUCT:
            {
                return{
                    ...state,
                    item:action.payload
                }
            }
        case SEND_EMAIL:
            {
                return{
                    ...state,
                    sendmail:action.payload
                }   
            }
        default:
            return state
    }
}
