import {SET_DETAIL_PRODUCT,SHOW_DIALOG,SEND_EMAIL} from './types'
import api from '../../services/ApiService.js'

export const setDetailProduct = (item) => dispatch=>{
    dispatch({
        type:SET_DETAIL_PRODUCT,
        payload:item
    })
}
export const sendMail = (param) => dispatch =>{

    api.post('/detail/sendmail',{param})
    .then(res=>{
        if(res.data.result == true)
        {
            dispatch({
                type:SHOW_DIALOG,
                payload:{
                    type:'success',
                    title:'Email Sent Success!',
                    msg:'Please check your mail app!'
                }
            })
        }else
        {
            dispatch({
                type:SHOW_DIALOG,
                payload:{
                    type:'fail',
                    title:'Email Sent Failed!',
                    msg:'Please try again!'
                }
            })
        }
        dispatch({
            type:SEND_EMAIL,
            payload:res.data.result
        })
    })
}