import React, {Component} from 'react';

import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import axios from 'axios'; 

import * as InquiryAction from '../../redux/actions/inquiryAction';
import * as AuthAction from '../../redux/actions/authAction';
import { 
    Timeline,
    TimelineItem,
    TimelineSeparator,
    TimelineConnector,
    TimelineContent,
    TimelineOppositeContent,
    TimelineDot,
 } from '@material-ui/lab';

import SocketContext from '../../socket-context'

import FastfoodIcon from '@material-ui/icons/Fastfood';
import LaptopMacIcon from '@material-ui/icons/LaptopMac';
import HotelIcon from '@material-ui/icons/Hotel';
import RepeatIcon from '@material-ui/icons/Repeat';

import { Paper , Typography } from '@material-ui/core'
import {DropzoneDialog} from 'material-ui-dropzone'

import ChatComponent from './ChatComponent'
import { TextField , Button, Chip }  from '@material-ui/core';

import DoneIcon from '@material-ui/icons/Done';
import CancelIcon from '@material-ui/icons/Cancel';
import NotInterestedIcon from '@material-ui/icons/NotInterested';
import RefreshIcon from '@material-ui/icons/Refresh';

import {Col, Container, Row} from 'reactstrap'

import {BASE_URL} from '../../services/ApiService'

import "./MyInquiry.css";
const StepTitle = [
    'InvoiceDoc',
    'PaymentProof',
    'Shipment Schedule'
]
const ChipColor = [
    'default',
    'secondary',
    'primary'
]
const DotColor=[
    'default',
    'primary',
]
const resultGroup = [
    {
        text:'OnGoing',
    },
    {
        text:'Done',
    },
    {
        text:'Expired',
    },
    {
        text:'Cancel',
    },
]
class MyOrderDetail extends Component{
    
    state={
        showUploadDlg:false
    }
    getInquiry = () =>{
        const { inquiryId } = this.props
        
        const { totalInquiry } = this.props.inquiry
        let returnVal = null
        returnVal = totalInquiry.find((item)=>{
            
            if(item.inquiry._id == inquiryId){
                
                return true
            }
        })
        return returnVal
    }
    refreshFlowChart = ()=>{
        let currentInquiry = this.getInquiry()
        this.props.refreshFlow({
            inquiryId:currentInquiry.inquiry._id
        })
    }
    emitRefreshEvent=()=>{
        let currentInquiry = this.getInquiry()
        this.props.socket.emit('refreshFlowChartFromUser',{
            inquiryId:currentInquiry.inquiry._id
        })
    }
    /**doc */
    uploadMultiFiles = (files,state) =>{
        //alert(state)
        //return
        let currentInquiry = this.getInquiry()
       
        const formData = new FormData()

        for (const file of files) {
            formData.append('files[]', file, file.name);
        }
        const config = {
            headers: {
                'content-type': 'multipart/form-data'
            },
            params:{
                mode:'inquiry',
                inquiryId:currentInquiry.inquiry._id

            }
        }
        axios.post(`${BASE_URL}/api/uploadfile`, formData,config)
        .then(res=>{
            let { files } = res.data
            this.props.uploadDoc({
                inquiryId:currentInquiry.inquiry._id,
                state:state,
                files:files
            })
            //this.emitRefreshEvent()
            this.setState({
                showUploadDlg:false
            })
        })
        
    }
    openDoc = (doc)=>{
       // alert(doc.path)
        window.open(`${BASE_URL}/upload/${doc.path}`);

    }
    deleteDoc = (doc) =>{
        //alert("de")
        this.props.deleteDoc({
            doc:doc
        })
    }
    /**--doc */

    render(){
        
        let item = this.getInquiry()
        let stateArray = [3,4,5]
       
        return(
            <Row className="TabContent">
                <Col md="5">
                    <div className="DetailBack">

                    <a onClick={()=>{this.props.setToggle(-1,false)}} style={{cursor:'pointer',padding:'10px'}}> Back to Inquiry List</a>
                    </div>
                    <div>
                    <Timeline align="alternate">
                    <TimelineItem>
                        <TimelineOppositeContent>
                            </TimelineOppositeContent>
                            <TimelineSeparator>
                                <TimelineDot color="primary" variant="outlined" style={{cursor:'pointer'}}>
                                            <RefreshIcon onClick={this.refreshFlowChart}/>
                                </TimelineDot>
                                <TimelineConnector />

                            </TimelineSeparator>
                            <TimelineContent>
                            </TimelineContent>
                        </TimelineItem>
                    {
                        stateArray.map((state,index)=>{
                            let passed = false
                            let userHandle = false
                            if(state < item.inquiry.state)
                            {
                                passed = true
                            }
                            if(state == 4)
                            {
                                userHandle = true
                            }
                            let docs = null
                            if(Array.isArray(item.inquirydocs)){
                                docs = item.inquirydocs.filter((doc)=>{
                                    if(doc.inquiryState == state)
                                        return true
                                    else
                                        return false
                                })
                            }
                            return(
                                <TimelineItem>
                                    <TimelineOppositeContent>
                                        <Paper elevation={3} style={{textAlign:'center'}}>
                                            <div style={{minHeight:'150px',padding:'10px 10px 30px 10px'}}>
                                                
                                                <Typography variant="h6" component="h1">
                                                    {StepTitle[state - 3]}
                                                </Typography>
                                                <div style={{minHeight:'50px'}}>
                                                {Array.isArray(docs) && (
                                                    <>
                                                    {docs.map((doc)=>{
                                                        if(state == 4){    
                                                            return(
                                                                <Chip
                                                                    label={doc.realName}
                                                                    color={`${ChipColor[doc.state]}`}
                                                                    onClick={()=>this.openDoc(doc)}
                                                                    onDelete={()=>{
                                                                        if(doc.state != 2)
                                                                          this.deleteDoc(doc)
                                                                    }
                                                                        
                                                                    }
//                                                                    deleteIcon={doc.state == 0 ? <CancelIcon/> : doc.state == 1 ? <NotInterestedIcon/> : <DoneIcon/>}
                                                                    deleteIcon={<CancelIcon/>}

                                                                    variant="outlined"
                                                                    style={{padding:'10px',margin:'5px'}}
                                                                >
                                                                
                                                                </Chip>
                                                            )
                                                        }
                                                        if(state != 4){    
                                                            return(
                                                                <Chip
                                                                    label={doc.realName}
                                                                    color={`${ChipColor[doc.state]}`}
                                                                    onClick={()=>this.openDoc(doc)}
                                                                    onDelete={()=>{}}
                                                                    deleteIcon={<DoneIcon/>}
                                                                    variant="outlined"
                                                                    style={{padding:'10px',margin:'5px'}}
                                                                >
                                                                
                                                                </Chip>
                                                            )
                                                        }
                                                    })}
                                                    </>
                                                )}
                                                {docs.length == 0 && (
                                                    <Typography style={{opacity:'.5'}} variant="h5" component="h1">No Doc yet!</Typography>   
                                                )}
                                                </div>
                                                
                                                {
                                                    userHandle && (//this means payment proof
                                                        <>
                                                        {item.inquiry.state != 4 && (   
                                                            <Button variant="outlined" fullWidth disabled onClick={()=>{this.setState({showUploadDlg:true})}}>Upload</Button>
                                                        )}
                                                        {item.inquiry.state == 4 && (   
                                                            <Button variant="outlined" fullWidth onClick={()=>{this.setState({showUploadDlg:true})}}>Upload</Button>
                                                        )}
                                                        
                                                        
                                                        <DropzoneDialog
                                                            open={this.state.showUploadDlg}
                                                            onSave={(files)=>this.uploadMultiFiles(files,state)}
                                                            acceptedFiles={['image/jpeg', 'image/png', 'image/bmp', 'image/json' ]}
                                                            showPreviews={true}
                                                            maxFileSize={5000000}
                                                            onClose={()=>{this.setState({showUploadDlg:false})}}
                                                        />
                                                        </>
                                                    )
                                                }
                                                
                                                
                                            </div>
                                        </Paper>
                                    </TimelineOppositeContent>
                                    <TimelineSeparator>
                                        <TimelineDot
                                            color={passed?'primary':state==item.inquiry.state ? 'secondary' :''}
                                        >
                                            <LaptopMacIcon />
                                        </TimelineDot>
                                        <TimelineConnector />
                                    </TimelineSeparator>
                                    <TimelineContent>
                                        {
                                            passed && (
                                                <>Passed</>
                                            )
                                        }
                                        {
                                            !passed && (
                                                <>NoPassed</>
                                            )
                                        }
                                    </TimelineContent>
                                </TimelineItem>
                            )
                        })
                    }
                        <TimelineItem>
                            <TimelineOppositeContent>
                            </TimelineOppositeContent>
                            <TimelineSeparator>
                                <div className="OrderResult">
                                    {resultGroup[item.inquiry.inquiryState].text}
                                </div>
                            </TimelineSeparator>
                            <TimelineContent>
                            </TimelineContent>
                        </TimelineItem>
                    </Timeline>
                    </div>
                </Col>
                <Col md="7">
                    <ChatComponent item={item}/>
                </Col>
            </Row>
        )
    }
}
const MyOrderDetailWithSocket = (props) => (
    <SocketContext.Consumer>
      {socket => <MyOrderDetail {...props} socket={socket} />}
    </SocketContext.Consumer>
  )
const mapStateToProps = (state) =>({
    auth    :state.auth,
    inquiry :state.inquiry
})

function mapDispatchToProps(dispatch)
{
    return bindActionCreators({ 
//      sendInvoice           :InquiryAction.sendInvoice,
        uploadDoc   :InquiryAction.uploadDoc,
        deleteDoc   :InquiryAction.deleteDoc,
       refreshFlow  :InquiryAction.refreshFlow

    }, dispatch);
}
export default connect(mapStateToProps,mapDispatchToProps)(MyOrderDetailWithSocket)