import React, {Component} from 'react';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import SocketContext from '../../socket-context'

import * as AuthAction from '../../redux/actions/authAction';
import {Link, withRouter} from 'react-router-dom';

import {Col, Container, Row} from 'reactstrap'
import Button from '@material-ui/core/Button'
import FaceBookIcon from '@material-ui/icons/Facebook'
import TextField from '@material-ui/core/TextField';
import { ValidatorForm, TextValidator } from 'react-material-ui-form-validator';
import DialogComponent from '../../components/DialogComponent';
import GoogleLogin from 'react-google-login';

class SigninComponent extends Component{
    state = {
        formData: {
            email:'',
            password: '',
        },
        submitted: false,
    };

    componentDidMount(){
      
        if ( this.props.auth.isAuthenticated ) {
			this.props.history.push( '/' );
        }
        
        ValidatorForm.addValidationRule('isPasswordMatch', (value) => {
            if (value !== this.state.formData.password) {
                return false;
            }
            return true;
        });
    }
    componentWillReceiveProps(nextProps) {
        if (nextProps.auth.isAuthenticated) {
          this.props.history.push("/");
        }
      }
    handleChange = (event) => {
        const { formData } = this.state;
        formData[event.target.name] = event.target.value;
        if (event.target.name === 'password') {
            this.form.isFormValid(false);
        }
        this.setState({ formData });
    }

    handleSubmit = () => {
       this.setState({
           submitted:false
       })
       this.props.loginUser(this.state.formData)
    }
    googleSuccess = (e) =>{
        /*
        alert('success')
        console.log(e)
        */
       let profile = e.profileObj
       console.log(e)
       this.props.loginUserWithGoogle(profile)
    }
    googleFail = (e) =>{
        //alert('fail')
        console.log(e)
    }
    render(){
    //    const { submitted } = this.state;
        const formsubmit = (this.props.auth.regResult == 1 || this.props.auth.regResult == 3)
        return(
            <Container fluid={true} style={{margin:'0px',padding:'0px'}}>

                <Row style={{height:'100vh',margin:'0px',padding:'0px'}}>

                    <Col lg="6" xs="12"  style={{display:'flex',justifyContent:'center',alignItems:'center',width:'50%'}}>
                        <div style={{}}>
                            <Row>
                                <h1>Sign In</h1>
                            </Row>
                            <Row>
                                <p>
                                 Please enter your email and password to enter the account
                                </p>
                            </Row>
                            <Row>
                                <p>
                                SignIn With:
                                </p>
                            </Row>
                            <Row>
                                <Col lg="6" xs="12" >
                                    <Button fullWidth color="primary" startIcon={<FaceBookIcon />} variant='outlined' style={{color:'rgba(73, 154, 236, 1)',height:'50px',margin:'5px'}}>Facebook</Button>
                                </Col>
                                <Col lg="6" xs="12">
                                    {/* <Button fullWidth color="primary" variant='outlined' style={{color:'rgba(73, 154, 236, 1)',height:'50px',margin:'5px'}}>Google</Button> */}
                                    <GoogleLogin
                                        clientId="240263392067-dn3bngvgf59op8h4fh5urnrsgu2c2i23.apps.googleusercontent.com"
                                        buttonText="Signin with Google"
                                        onSuccess={this.googleSuccess}
                                        onFailure={this.googleFail} 
                                        cookiePolicy={'single_host_origin'}
                                        render={renderProps => (
                                            //<button onClick={renderProps.onClick} disabled={renderProps.disabled}>This is my custom Google button</button>
                                            <Button fullWidth onClick={renderProps.onClick} disabled={renderProps.disabled} color="primary" variant='outlined' style={{color:'rgba(73, 154, 236, 1)',height:'50px',margin:'5px'}}>Google</Button>
                                          )}
                                        >
                                    </GoogleLogin>
                                </Col>
                            </Row>
                            <Row>
                                <ValidatorForm
                                    ref={r => (this.form = r)}
                                    onSubmit={this.handleSubmit}
                                    style={{
                                        width:'100%'
                                    }}
                                >
                                
                                    
                                    <TextValidator
                                        id="outlined-password-input"
                                        onChange={this.handleChange}
                                        name="email"

                                        label="Email"
                                        type="email"
                                        variant="outlined"
                                        validators={['required']}
                                        errorMessages={['this field is required']}

                                        fullWidth
                                        style={{marginBottom:'15px'}}
                                        value={this.state.formData.email}
                                    />
                                    <TextValidator
                                        label="Password"
                                        onChange={this.handleChange}
                                        name="password"
                                        type="password"
                                        variant="outlined"
                                        fullWidth

                                        validators={['required']}
                                        errorMessages={['this field is required']}
                                        value={this.state.formData.password}
                                    />
                                    <br />
                                    <Button
                                        color="primary"
                                        variant="contained"
                                        type="submit"
                                        fullWidth
                                    //    disabled={formsubmit}
                                        style={{backgroundColor:'rgba(73, 154, 236, 1)',height:'50px',marginBottom:'30px'}}
                                    >
                                        {
                                            // (formsubmit && 'Your form is submitted!')

                                            // || (!formsubmit && 'Registration')
                                            'Sign In'
                                        }

                                    </Button>
                                    
                                    <div style={{textAlign:'center'}}>
                                    Don't have an account? <Link to="/signup"><Button style={{color:'rgba(73, 154, 236, 1)'}}>Sign Up</Button></Link>
                                    </div>

                                </ValidatorForm>

                               
                            </Row>
                           
                        </div>
                    </Col>
                    <Col lg="6" xs="12" style={{margin:'0px',padding:'0px'}}>
                        <img src="./static/assets/img/car/1.jpg" style={{width:'100%',height:'100%'}}></img>
                    </Col>
                    {/* <DialogComponent/> */}
                </Row>
            </Container>
        )
    }
}
const SignincomponentWithSocket = (props) => (
    <SocketContext.Consumer>
        {socket => <SigninComponent {...props} socket={socket}/>}
    </SocketContext.Consumer>
)
const mapStateToProps = (state) =>({
    auth:state.auth
})

function mapDispatchToProps(dispatch)
{
    return bindActionCreators({      
        loginUser :AuthAction.loginUser,
        loginUserWithGoogle:AuthAction.loginUserWithGoogle
    }, dispatch);
}
export default connect(mapStateToProps,mapDispatchToProps)(SignincomponentWithSocket)