import React, {Component} from 'react';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';

import * as HomeAction from '../../redux/actions/homeAction';
import * as GlobalAction from '../../redux/actions/globalAction';

import { Row,Col } from 'reactstrap'
import {withStyles, MuiThemeProvider} from '@material-ui/core/styles';

import Typography from '@material-ui/core/Typography'
import MenuItem from '@material-ui/core/MenuItem';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Select from '@material-ui/core/Select';
import Divider from '@material-ui/core/Divider';
import CarInfoItem from './CarInfoItem'
import StyledDropDown from '../Utility/StyledDropDown/StyledDropDown'
import MuiPagination from '@material-ui/lab/Pagination';
import MyDropDown from '../Utility/MyDropDown'

const styles = theme => ({
    root: {
        maxWidth: 345,
      },
      media: {
        height: 140,
      }
})
const Pagination = withStyles({
    ul:{

    }
    
  })(MuiPagination);
class CarInfo extends Component{
    state={
        sortby:"Sort By",
        currency:"USD",
        currentPage:1,
        currentItemPerPage:12,
        currentPageCars:[],
        currencyDropDownList:[
            {
                firstimage:'./static/assets/img/flag/us.png',
                title:'USD'
            },
            {
                firstimage:'./static/assets/img/flag/eu.png',
                title:'EUR'
            },
            {
                firstimage:'./static/assets/img/flag/aud.png',
                title:'AUD'
            }
        ],
        sortDropDownList:[
            {
                value:0,
                title:'Date (Down)'
            },
            {
                value:1,
                title:'Date (Up)'
            },
            {
                value:2,
                title:'Price (Down)'
            },
            {
                value:3,
                title:'Price (Up)'
            },
        ]
    }
    componentDidMount(){
       this.props.setFilter(this.props.filter)
       this.onPageChange(null,1)
    }
    componentWillReceiveProps(nextProps)
    {
        if(nextProps.cars != this.props.cars || nextProps.sortBy != this.props.sortBy)
        {
            this.onPageChange(null,1,nextProps)
        }
    }
    setSortByChange = (event) =>{
        this.setState({
            sortby:event.target.value
        })
    //    this.props.setFilter(this.props.filter,event.target.value)
        //if(event.target.value >= 0 && )
        this.props.setSortBy(event.target.value)
    }
    setCurrencyChange = (event) =>{
        /*
        this.setState({
            currency:event.target.value
        })
        */
       this.props.setCurrentCurrency(event.target.value)
    }
    setSortDropDownList = (index,element)=>{
        this.setState({
            sortby:index
        })
        this.props.setSortBy(index)

    }
    setCurrencyDropdownChange = (index,element)=>{
        this.props.setCurrentCurrency(element.title)
    }
    onPageChange = (event, value,prop = null) => {
        
         let cars = this.props.cars
         if(prop != null)
            cars = prop.cars
        this.setState({
            currentPage:value,
            currentPageCars:cars.slice(this.state.currentItemPerPage * (value - 1),this.state.currentItemPerPage * (value - 1) + this.state.currentItemPerPage)
        })
    };

    render(){
    const {classes} = this.props;
    const {cars} = this.props;
    const {currentPage,currentItemPerPage,currentPageCars , currencyDropDownList , sortDropDownList} = this.state;
    //dropdown
    let currencyHeader 
    currencyDropDownList.map((item,index)=>{
        if(item.title == this.props.global.currentCurrency)
        {
            currencyHeader = {
                firstimage:item.firstimage,
                title:item.title
            }
        }
    })
    let sortHeader
    sortDropDownList.map((item,index)=>{
        if(item.value == this.props.sortBy)
        {
            sortHeader = {
                title:item.title,
               
            }
        }
    })
    return(
        <div>
            <Row style={{height:'80px'}}>
                <Col xl="6" lg="6" sm="12">
                    <div className="colContent-left">
                    <Typography>Search Result ({cars.length})</Typography>
                    </div>
                </Col>
                <Col xl="6" lg="6" sm="12">
                    <Row >
                        <Col xs="6">
                           <div className="colContent-left">

                            <MyDropDown header={currencyHeader} dropdownlist={currencyDropDownList} hasoutline={true} color='primary' isCheck={true} onChange={this.setCurrencyDropdownChange}></MyDropDown>
                            </div>
                        </Col>
                        <Col xs="6">
                            <div className="colContent-left">

                            <MyDropDown header={sortHeader} dropdownlist={sortDropDownList} hasoutline={true} color='primary' isCheck={true} onChange={this.setSortDropDownList}></MyDropDown>
                            </div>
                        </Col>
                        {/* <Col xs="0">
                            <StyledDropDown
                                labelId="demo-simple-select-outlined-label"
                                id="demo-simple-select-outlined"
                                value={this.props.sortBy}
                                variant="outlined"
                                onChange={this.setSortByChange}
                                fullWidth
                                style={{
                                    maxHeight:'40px'
                                }}
                                >
                                <MenuItem value="Sort By">
                                Sort By
                                </MenuItem>
                                <MenuItem value={0}>Date(Down)</MenuItem>
                                <MenuItem value={1}>Date(Up)</MenuItem>
                                <MenuItem value={2}>Price(Down)</MenuItem>
                                <MenuItem value={3}>Price(Up)</MenuItem>
                            </StyledDropDown>
                        </Col> */}
                        {/* <Col xs="6">
                            <Select
                                labelId="demo-simple-select-outlined-label"
                                id="demo-simple-select-outlined"
                                //value={this.state.currency}
                                value={this.props.global.currentCurrency}
                                variant="outlined"
                                onChange={this.setCurrencyChange}
                                fullWidth
                                style={{
                                    maxHeight:'40px'
                                }}
                                >
                                <MenuItem value="USD">
                                    USD
                                </MenuItem>
                                <MenuItem value={'EUR'}>EUR</MenuItem>
                                <MenuItem value={'AUD'}>AUD</MenuItem>
                            </Select>
                        </Col> */}
                    </Row>
                </Col>
            </Row>
            <Divider></Divider>
            
            <Row>
                {
                    currentPageCars.map((item,index)=>{
                         let isfavorite = this.props.favorite.indexOf(item._id) == -1 ? false:true
                        if(item.invoiceState == 3){
                            return(
                                <></>
                            )
                        }else
                        {
                            if(!this.props.isShowFilter){
                                return(
                                    <Col xl='2' lg='3' md='4' sm='6' xs='12' style={{
                                        display:'flex',
                                        justifyContent:'center'
                                    }}>
                                        <div className="colContent-left">

                                        {/* <CarInfoItem key={index} info={item} currency={this.state.currency}/> */}
                                        <CarInfoItem key={index} isfavorite={isfavorite} info={item} currency={this.props.global.currentCurrency}/>
                                        </div>
                                    </Col>
                                )
                            }else
                            {
                                return(
                                    <Col xl='3' lg='4' md='6' sm='6' xs='12' style={{
                                        display:'flex',
                                        justifyContent:'center'
                                    }}>
                                        <div className="colContent-left">

                                        {/* <CarInfoItem key={index} info={item} currency={this.state.currency}/> */}
                                        <CarInfoItem key={index} isfavorite={isfavorite} info={item} currency={this.props.global.currentCurrency}/>
                                        </div>
                                    </Col>
                                )
                            }
                        }
                        
                    })
                }
                
            </Row>
            <Row>
                <Col style={{
                    display:'flex',
                    justifyContent:'center'
                }}>
                <Pagination count={Math.ceil(cars.length / currentItemPerPage)} page={currentPage} onChange={this.onPageChange} variant="outlined" shape="rounded" />
                </Col>
            </Row>
        </div>
        )
    }
}
const mapStateToProps = (state) =>({
    cars                :state.home.cars,
    filter              :state.home.advancedfilter,
    favorite            :state.home.favorite,
    favCnt              :state.home.favCnt,
    sortBy              :state.home.sortBy,
    global              :state.global
})

function mapDispatchToProps(dispatch)
{
    return bindActionCreators({      
        getCar              :HomeAction.getCar,
        setFilter           :HomeAction.setFilter,
        setSortBy           :HomeAction.setSortBy,
        setCurrentCurrency  :GlobalAction.setCurrentCurrency

    }, dispatch);
}
export default connect(mapStateToProps,mapDispatchToProps)((withStyles(styles, {withThemem: true})(CarInfo)));