import React, {Component} from 'react';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import * as InquiryAction from '../../redux/actions/inquiryAction';
import * as AuthAction from '../../redux/actions/authAction';

import ChatComponent from './ChatComponent'
import { TextField , Button, Paper }  from '@material-ui/core';
import {Col, Container, Row} from 'reactstrap'
import "./MyInquiry.css";

class MyInquiryDetail extends Component{
    
    getInquiry = () =>{
        const { inquiryId } = this.props
        
        const { totalInquiry } = this.props.inquiry
        let returnVal = null
        returnVal = totalInquiry.find((item)=>{
            
            if(item.inquiry._id == inquiryId){
                
                return true
            }
        })
        return returnVal
    }
    onSendInvoice = () =>{
        let item = this.getInquiry()
       
        this.props.sendInvoice({
            inquiryId:item.inquiry._id
        })
    }
    render(){
        
        let item = this.getInquiry()
        const imgpath = item.inquiry.car.images.length == 0 ? "":item.inquiry.car.images[0]
        
        return(
            <Row className="TabContent">
                <Col md="5">
                    <div className="DetailBack">
                        <a onClick={()=>{this.props.setToggle(-1,false)}} style={{cursor:'pointer',padding:'10px'}}> Back to Inquiry List</a>
                    </div>
                    <div>
                        <Paper style={{padding:'10px'}}>
                        <div className="InquiryContent-Content-One">
                                <img style={{borderRadius:'50%',width:'50%'}} src={imgpath}></img>
                                <p>{item.inquiry.car.make} {item.inquiry.car.model}</p>
                                <p className="Opacity70">{item.inquiry.car.odometer} m.</p>
                                <p className="Opacity70">{item.inquiry.car.engine_size} {item.inquiry.car.engine_size_unit}</p>
                        </div>
                        <div className="InquiryContent-Content-One">
                            <p>INQUIRY</p>
                            {item.inquiry.state == 2 && (
                                <p>
                                    Invoice sent.
                                </p>
                            )}
                            {item.inquiry.state == 1 && (
                                <Button
                                    variant="contained"
                                    fullWidth
                                    onClick={this.onSendInvoice}
                                    className="NormalBtn InquiryContent-Content-Second-Input-Button"
                                >
                                    Ask Invoice
                                </Button>
                            )}
                            
                        </div>
                        </Paper>
                    </div>
                </Col>
                <Col md="7">
                    <ChatComponent item={item}/>
                </Col>
            </Row>
        )
    }
}
const mapStateToProps = (state) =>({
    auth    :state.auth,
    inquiry :state.inquiry
})

function mapDispatchToProps(dispatch)
{
    return bindActionCreators({ 
      sendInvoice           :InquiryAction.sendInvoice,
    }, dispatch);
}
export default connect(mapStateToProps,mapDispatchToProps)(MyInquiryDetail)