import React, {Component} from 'react';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import { animateScroll } from "react-scroll";

import SocketContext from '../../../socket-context'
import {Col, Container, Row} from 'reactstrap'
import { Button , TextField } from '@material-ui/core'
import * as AdminAction from '../../../redux/actions/adminAction';
import * as AuthAction from '../../../redux/actions/authAction';
import AdminChatComponent from '../AdminChatComponent'
import '../Admin.css'
class AdminInquiryDetail extends Component{
    state={
        msg:''
    }
    componentDidMount(){

    }

    getCurrentInquiry = ()=>{
        let returnVal = null
        console.log(this.props.currentinquiry)
        
        if(Array.isArray(this.props.admin.totalInquiry))
        {
            this.props.admin.totalInquiry.map((ite,index)=>{
                if(ite.inquiry._id == this.props.currentinquiry){
                    returnVal = ite
                }
            })
        }
        console.log(returnVal)
        return returnVal
    }

    onAcceptInvoice = () =>{
        this.props.acceptInvoice({
            uid         :this.props.auth.user.id,
            invoiceId   :this.props.currentinquiry
        })
        this.props.onBack()
    }
    render(){
        const currentInquiry = this.getCurrentInquiry()
        return(
            <div style={{width:'100%'}}>
                <div style={{
                    display: 'flex',
                    justifyContent: 'space-between',
                    paddingLeft: '20px',
                    paddingRight: '20px'
                }}>
                
                    <Button
                                variant="contained"
                                style={{backgroundColor:'#fc5555!important'}}
                                onClick={this.props.onBack}
                            >
                                Back
                    </Button>
                    <Button
                                variant="contained"
                                onClick={()=>{this.onAcceptInvoice()}}
                                className="NormalBtn"
                            >
                                Accept Invoice
                    </Button>
                    </div>
                    
                    <AdminChatComponent currentInquiry={currentInquiry} />
                
            </div>
        )
    }
}
const AdminInquiryDetailWithSocket = (props) => (
    <SocketContext.Consumer>
      {socket => <AdminInquiryDetail {...props} socket={socket} />}
    </SocketContext.Consumer>
  )
const mapStateToProps = (state) =>({
    auth    :state.auth,
    admin :state.admin
})

function mapDispatchToProps(dispatch)
{
    return bindActionCreators({ 
        acceptInvoice         :AdminAction.acceptInvoice,
        checkDoneInquiryMsg   :AdminAction.checkDoneInquiryMsg
    }, dispatch);
}
export default connect(mapStateToProps,mapDispatchToProps)(AdminInquiryDetailWithSocket)