import React, {useState,useEffect} from 'react'
import { useHistory } from "react-router-dom";
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import * as AuthAction from '../../redux/actions/authAction';
import * as GlobalAction from '../../redux/actions/globalAction';

import * as HomeAction from '../../redux/actions/homeAction';
import * as InquiryAction from '../../redux/actions/inquiryAction';
import SocketContext from '../../socket-context'

import {withStyles, MuiThemeProvider} from '@material-ui/core/styles';

import {Nav, Navbar, NavbarToggler, Collapse, NavItem, NavLink, NavbarBrand,NavbarText,Dropdown} from 'reactstrap'
import Button from '@material-ui/core/Button';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import Menu from '@material-ui/core/Menu';
import {Link} from 'react-router-dom';
import MyDropDown from '../Utility/MyDropDown'
import './Navbar.css';
import '../../App.css';
import {BASE_URL} from '../../services/ApiService'

const styles = theme =>({

})

const NavComponent = (props) => {
    let history = useHistory();
    const [isOpen, setIsOpen] = useState(false)
    const [country, setCountry] = useState('Arial')
    const [locationDropDown, setlocationDropDownOpen] = useState(false);
    const [adminDropDownHeader , setAdminDropDownHeader] = useState({})
    const [adminDropDonwList , setAdminDropDownList] = useState([])
    const [countryDropDownHeader , setCountryDropDownHeader] = useState([])
//    const [newChatLogs , setNewChatLogs] = useState([])
    const countryDropDownList = [
        {
            firstimage:'./static/assets/img/flag/aud.png',
            title:'Australia',
            value:0
        },
        {
            firstimage:'./static/assets/img/flag/us.png',
            title:'USA',
            value:1

        },
        {
            firstimage:'./static/assets/img/flag/eu.png',
            title:'Europe',
            value:2

        },
    ]

    const toggle = () =>{ 
        setlocationDropDownOpen(!locationDropDown)
            
    }
    useEffect(()=>{
        if(props.auth.isAuthenticated){
            //alert("asdf")
            props.socket.emit('getNavBarAlert',props.auth.user)
        }
        props.socket.on('navBarAlert',data=>{
            
        })
        
    },[])
    useEffect(() => {
        // Update the document title using the browser API
        countryDropDownList.map((item,index)=>{
            if(index == props.home.country)
            {
                setCountryDropDownHeader({
                    firstimage:item.firstimage,
                    title:item.title,
                })
            }
        })
        if(props.auth.isAuthenticated){
            let avatarUrl = `${BASE_URL}/upload/avatar/`
            avatarUrl = props.auth.user.avatar?`${avatarUrl}${props.auth.user.avatar}`:`${avatarUrl}default.png`
            setAdminDropDownHeader({
                title:props.auth.user.name,
                firstimage:avatarUrl
            })
            setAdminDropDownList([
              
                {
                    title:'My Inquiry',
                    firstimage:'./static/assets/img/myinquiry.png',
                },
                {
                    title:'My Orders',
                    firstimage:'./static/assets/img/myorders.png',

                },
                {
                    title:'Settings',
                    firstimage:'./static/assets/img/settings.png',

                },
                {
                    title:'Favorites',
                    firstimage:'./static/assets/img/favorites.png',

                },
                
                {
                    title:'Sign Out',
                    firstimage:'./static/assets/img/myinquiry.png',

                },
            ])
        }

      },[props]);
      
    const toggleNav = () => {
        setIsOpen(!isOpen)
    }
    const CountryChange = (e) =>{
        setCountry(e.target.value)
        console.log(props.auth)
    }
    const onDashboard = (e) =>{
        e.preventDefault()
    }
    const onInquiry = (e) =>{
        e.preventDefault()
       // props.history.push('./myinquiry')
    }
    const onLogout = (e) =>{
        e.preventDefault()
        props.logoutUser()
    }
    const setCountryDropdownChange = (index,element) =>{
        props.selectCountry(index)
       
    }
    const clickAdminDropdown = (index , element)=>{
        //alert(index)
        switch(element.title){
            case 'My Inquiry':
            case 'My Orders':
            case 'Settings':
               // props.history.push('./myinquiry')
                history.push(
                    '/myinquiry'
                  )
                props.setCurrentInquiryPage(index)
                break
            case 'Favorites':
                break
            
            
            case 'Sign Out':
                props.logoutUser()
                break
        }
    }
    return(
            <Navbar light expand = "lg" className="MyNavBar">
      
                <NavbarBrand href="/">LOGO</NavbarBrand>
                <NavbarToggler onClick={toggleNav}></NavbarToggler>
                <Collapse isOpen = {isOpen} navbar>
                    <Nav className = "ml-auto" navbar>
                        <NavItem>
                            <Link to = "/" className="nav-link"><Button fullWidth color="light" style={{minWidth:'150px'}}>HOW TO BUY</Button></Link>
                        </NavItem>

                        <NavItem style={{minWidth:'200px'}}>
                            <MyDropDown header={countryDropDownHeader} dropdownlist={countryDropDownList} hasoutline={false} color='primary' isCheck={true} onChange={setCountryDropdownChange}></MyDropDown>                            
                        </NavItem> 
                    
                        {
                            props.auth.isAuthenticated == false && (<>
                                <NavItem>
                                    <Link to = "/signin" className="nav-link"><Button fullWidth  color="primary" style={{minWidth:'150px'}}>Sign In</Button></Link>
                                </NavItem>

                                <NavItem>
                                    <Link to = "/signup" className="nav-link"><Button fullWidth variant='contained' style={{backgroundColor:'rgba(73, 154, 236, 1)',minWidth:'150px'}} color="primary" block>Sign Up</Button></Link>
                                </NavItem>
                                </>)
                        }
                        {
                            props.auth.isAuthenticated == true && (
                            <>
                                <NavItem style={{minWidth:'200px'}}>
                                    <MyDropDown header={adminDropDownHeader} dropdownlist={adminDropDonwList} hasoutline={false} color='secondary' isCheck={false} onChange={clickAdminDropdown}></MyDropDown>                            
                                </NavItem>
                                
                                {
                                    (props.auth.user.name=="admin" && (
                                        <NavItem>
                                            <Link to = "/admin/" className="nav-link"><Button fullWidth  color="primary" style={{minWidth:'150px'}}>Admin Dashboard</Button></Link>
                                        </NavItem>
                                    ))
                                }
                            </>)
                        }
                    </Nav>
                </Collapse>
            </Navbar>
  
    )
}
const NavComponentWithSocket = (props) => (
    <SocketContext.Consumer>
      {socket => <NavComponent {...props} socket={socket} />}
    </SocketContext.Consumer>
  )
const mapStateToProps = (state) =>({
    auth:state.auth,
    home:state.home
})
function mapDispatchToProps(dispatch)
{
    return bindActionCreators({
        selectCountry           :HomeAction.setCountry,
        setCurrentInquiryPage   :InquiryAction.setCurrentPage,
        //setNewChatLogs          :GlobalAction.setNewChatLogs,
        logoutUser              :AuthAction.logoutUser
    },dispatch);
}
export default (withStyles(styles, {withThemem: true})(connect(mapStateToProps,mapDispatchToProps)(NavComponentWithSocket)))