import React, {Component} from 'react';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import * as AdminAction from '../../../redux/actions/adminAction';

import {Col, Container, Row} from 'reactstrap'
import {TextField,MenuItem,Tab, Tabs,Button, Dialog, DialogActions, DialogContent, Icon, IconButton, Typography, Toolbar, AppBar, Avatar} from '@material-ui/core';

import ReactTable from 'react-table-6';
import "react-table-6/react-table.css";
import api from '../../../services/ApiService.js'
  /**
     * 0:default
     * 1:inquiry
     * 2:ask invocie but not confirm
     * 3:confirm invoice
     * 4:waiting payment proof
     * 5:shipment schedule
     * 6:done
     * 
     */
const invoiceStateString = [
    ''
]
class AdminCarManage extends Component{

    state = {
        cars:[],
        pages       :0,

    }
    constructor(props)
    {
        super(props)
        this.table = React.createRef();
    }
    componentDidMount(){
        // this.props.getAskInvoiceCar()
        this.props.setTitle("CarManage")
                
    }
    fetchdata = (state, instance) => {
       console.log(state)
        api.get('/cars/getAllCar',{
            params:{
                pgsize  :state.pageSize,
                pg      :state.page
            }
        }).then(res=>{
            this.setState({
                cars        : res.data.cars,
                pages       : res.data.pgcnt,
                loading: false
             })
        })
        
    
    }
    render(){
        return(
            <div>
                <ReactTable
                                ref={this.table}
                                className="InvoiceTable"
                                getTrProps={(state, rowInfo, column) => {
                                    return {
                                        className: "cursor-pointer",
                                        onClick  : (e, handleOriginal) => {
                                            if ( rowInfo )
                                            {
                                                console.log(rowInfo.original)
                                              //  this.onShowDetail(true,rowInfo.original)
                                                //this.openEditDialog(rowInfo.original)
                                            }
                                        }
                                    }
                                }}
                                columns={[
                                    {
                                        Header:()=>{
                                            return(
                                                 <div></div>
                                             )
                                        },
                                        accessor:"images",
                                        
                                        Cell:t=>{
                                            return (
                                                // <img className="InvoiceTableImage" src={t.value}></img>
                                                // <div className="Text2 Inquiry-Table-Body-Item">{t.value[0]}</div>
                                                <img className="InvoiceTableImage" src={t.value[0]}></img>


                                            )
                                        }
                                    },
                                    {
                                        Header:()=>{
                                            return(
                                                 <div>name</div>
                                             )
                                        },
                                        accessor:"name",
                                        
                                        Cell:t=>{
                                            return (
                                                // <img className="InvoiceTableImage" src={t.value}></img>
                                                <div className="Text2 Inquiry-Table-Body-Item">{t.value}</div>

                                            )
                                        }
                                    },
                                    {
                                        Header:()=>{
                                            return(
                                                 <div className="Text1 Inquiry-Table-Title-Item">State</div>
                                             )
                                        },
                                        accessor:"invoiceState",
                                        
                                        Cell:t=>{
                                            return (
                                                <div className="Text2 Inquiry-Table-Body-Item">{t.value}</div>
                                            )
                                        }
                                    },
                                    {
                                        Header:()=>{
                                            return(
                                                 <div className="Text1 Inquiry-Table-Title-Item">PRICE</div>
                                             )
                                        },
                                        accessor:"price",
                                        
                                        Cell:t=>{
                                            return (
                                                <div className="Text2 Inquiry-Table-Body-Item">US$ {t.value[0].USD}</div>
                                            )
                                        }
                                    },
                                    {
                                        Header:()=>{
                                            return(
                                                 <div className="Text1 Inquiry-Table-Title-Item">BRAND</div>
                                             )
                                        },
                                        accessor:"body_type",
                                        
                                        Cell:t=>{
                                            return (
                                                <div className="Text2 Inquiry-Table-Body-Item">{t.value}</div>
                                            )
                                        }
                                    },
                                    {
                                        Header:()=>{
                                            return(
                                                 <div className="Text1 Inquiry-Table-Title-Item">BODYTYPE</div>
                                             )
                                        },
                                        accessor:"inquiry.car.body_type",
                                        
                                        Cell:t=>{
                                            return (
                                                <div className="Text2 Inquiry-Table-Body-Item">{t.value}</div>
                                            )
                                        }
                                    },
                                    
                                   
                                ]}
                                defaultPageSize={5}
                                manual
                                noDataText="No contents found"
                                pages={this.state.pages}

                                data={this.state.cars}
                                onFetchData={this.fetchdata}
                            />
            </div>
        )
    }
}

const mapStateToProps = (state) =>({
    auth        :state.auth,
    admin :state.admin
})

function mapDispatchToProps(dispatch)
{
    return bindActionCreators({
        setTitle:AdminAction.setTitle,
        // getAllCars  :AdminAction.getAllCars
    }, dispatch);
}
export default connect(mapStateToProps,mapDispatchToProps)(AdminCarManage)