import {
    HOME_GET_GLOBAL_DATA,
     GET_CAR,
     SET_ADVANCED_FILTER,
     CHANGE_SORTBY,
     SET_FAVORITE,
     SET_SHOWFILTER,
     SET_COUNTRY
} from './types'

import api from '../../services/ApiService.js'
export const setCountry = (index) => dispatch=>{
    dispatch({
        type:SET_COUNTRY,
        payload:index,
    })
}
export const getGlobalData = (param) =>dispatch =>{
    api.get('/users/getglobaldata',{
        params:param
    })
    .then(res=>{
        /*
        dispatch({
            type:HOME_GET_GLOBAL_DATA,
            payload:res.data.favorites
        })
        */
    })
}
export const setFilter = (param) => dispatch=>{
    getCarWithFilter({
        'advanced_filter':param,
   //     'sortby':sortby
    },dispatch)
    
    dispatch({
        type:SET_ADVANCED_FILTER,
        payload:param,
   //     sortBy:sortby
    })
    
}
export const getCarWithFilter = (filter,dispatch) =>{
    console.log(filter)
    api.get('/cars/filter',{
        params:filter
        
    })
    .then(res=>{
        console.log('advanced')
        console.log(res.data)
        dispatch({
            type:GET_CAR,
            payload:res.data
        })
    })
}
export const getCar = (param) => dispatch =>{

    api.get('/cars',{param})
    .then(res=>{
        console.log('car')
        console.log(res.data)
        dispatch({
            type:GET_CAR,
            payload:res.data
        })
    })
}
export const setSortBy = (sortIndex) => dispatch=>{
    //console.log('sortybyaction')
    dispatch({
        type:CHANGE_SORTBY,
        payload:sortIndex
    })
}
export const setFavorite = (param) => dispatch=>{
    /*
    api.post('/users/setfavorite',{
        param
    })
    .then(res=>{
        
    })
    */
    dispatch({
        type:SET_FAVORITE,
        payload:param.carid
    })
    
}
export const setShowFilter = () =>dispatch=>{
    dispatch({
        type:SET_SHOWFILTER
    })
}