const {SET_COUNTRY,HOME_GET_GLOBAL_DATA,GET_CAR,SET_ADVANCED_FILTER,CHANGE_SORTBY,SET_FAVORITE,SET_SHOWFILTER} = require('../actions/types');


const initialState = {
    country:0,
    cars : [],
    advancedfilter : {
    //    expanedList:[false,false,false,false,false,false,false],
        PopularBrandFilter:[],
        BodyTypeFilter:[],
        MileageFilter:[0,500000],
        PriceFilter:[0,50000],
        CarUsageFilter:[],
        EngineFilter:[0,7000],

        Transmission:[false,false],
        Drive:[false,false],
        Fuel:[false,false,false,false],
        Steer:[false,false]
    },
    sortBy : 0,
    currencyfilter:0,
    favorite:[],
    favCnt:0,
    isShowFilter:true
}
const sortBytIndex = (sortIndex,cars) =>{

    if(sortIndex == 2 || sortIndex == 3)
    {
        cars = cars.sort((a,b)=>{
            return sortIndex == 3 ? a.price[0].AUD - b.price[0].AUD : b.price[0].AUD - a.price[0].AUD
        })
    }
    if(sortIndex == 0 || sortIndex == 1)
    {

        cars = cars.sort((a,b)=>{
            return sortIndex == 1 ? a.build_year - b.build_year : b.build_year - a.build_year
        })
        
    }
    console.log(cars)

    return cars
}
export default function(state = initialState, action){
    switch(action.type){
        case SET_COUNTRY:
            {
                return {
                    ...state,
                    country:action.payload
                }
            }
        case HOME_GET_GLOBAL_DATA:
            {
                return{
                    ...state,
                    favorite:action.payload,
                    favCnt:action.payload.length
                }
            }
        case SET_FAVORITE:
            {
                
                let favorite = [...state.favorite]
                let id = action.payload

                let index = favorite.indexOf(id)
                if(index == -1)
                {
                    favorite.push(id)
                    
                }else
                {
                    favorite.splice(index,1)
                }
                return{
                    ...state,
                    favorite:favorite,
                    favCnt:favorite.length
                }
               
            }
        case GET_CAR:
            {
                let cars = action.payload.car
                cars = sortBytIndex(state.sortBy,cars)
                return {
                    ...state,
                    cars:action.payload.car
                }
            }
        case SET_ADVANCED_FILTER:
            {
                return {
                    ...state,
                    advancedfilter  :action.payload,
                //    sortBy          :action.sortBy
                }
            }
        case CHANGE_SORTBY:
            {
                console.log(action.payload)
                let sortIndex = action.payload
                let cars = state.cars
                cars = sortBytIndex(sortIndex,cars)
                return{
                    ...state,
                    sortBy:sortIndex,
                    cars:cars
                }
            }
        
        case SET_SHOWFILTER:
            {
                return {
                    ...state,
                    isShowFilter:!state.isShowFilter
                }
            }
        default:
            return state;
    }
}
